/**
 * é˜²æŠ–å‡½æ•°
 * @param func å®é™…è°ƒç”¨çš„å›è°ƒ
 * @param wait 2æ¬¡å›è°ƒæ‰§è¡Œçš„æœ€çŸ­æ—¶é—´é—´éš”
 * @param immediate æ˜¯å¦ç«‹å³æ‰§è¡Œå›è°ƒ
 *
 */
function debounce(func, wait = 16, immediate) {
  let timer, result
  let debounced = function () {
    let context = this
    let args = arguments

    // åœ¨ç¬¬ä¸€æ¬¡äº‹ä»¶è§¦å‘çš„æ—¶å€™å°±æ‰§è¡Œï¼Œæ­¤åè§¦å‘çš„äº‹ä»¶éƒ½ä¸å†æ‰§è¡Œ
    if (immediate) {
      if (timer) {
        clearTimeout(timer)
      } else {
        result = func.apply(context, args)
      }
      timer = setTimeout(function () {
        timer = null
      }, wait)

      // åªåœ¨æœ€åä¸€æ¬¡äº‹ä»¶è§¦å‘çš„é—´éš”ä¹‹åæ‰§è¡Œ
    } else {
      clearTimeout(timer)
      timer = setTimeout(function () {
        func.apply(context, args)
      }, wait)
    }

    return result
  }

  debounced.cancel = function () {
    clearInterval(timer)
    timer = null
  }

  return debounced
}

export default debounce
