/**
 * screenChange <Function> ä¸ºçª—å£å˜åŒ–çš„ çš„å›è°ƒå‡½æ•°  å‚æ•°ä¸ºå½“å‰æ˜¯å¦ä¸ºå…¨å±çŠ¶æ€
 */
export const init = (screenChange) => {
  // å–å€¼17æ˜¯ä¸ºäº†å¤„ç†é¡µé¢å†…å®¹å‡ºç°æ»šåŠ¨æ¡çš„æƒ…å†µ
  let isFull =
    window.screen.height - window.document.documentElement.clientHeight <= 17

  // é˜»æ­¢F11é”®é»˜è®¤äº‹ä»¶ï¼Œç”¨HTML5å…¨å±APIä»£æ›¿
  window.addEventListener('keydown', function (e) {
    e = e || window.event
    if (e.keyCode === 122 && !isFull) {
      e.preventDefault()
      enterFullScreen()
    }
  })
  //ç›‘å¬çª—å£å˜åŒ–
  window.onresize = function () {
    isFull =
      window.screen.height - window.document.documentElement.clientHeight <= 17
    screenChange(isFull)
  }
}

//è¿›å…¥å…¨å±
export const enterFullScreen = () => {
  let el = document.documentElement
  let rfs =
    el.requestFullScreen ||
    el.webkitRequestFullScreen ||
    el.mozRequestFullScreen ||
    el.msRequestFullscreen
  if (rfs) {
    // typeof rfs != "undefined" && rfs
    rfs.call(el)
  } else if (typeof window.ActiveXObject !== 'undefined') {
    // for IEï¼Œè¿™é‡Œå…¶å®å°±æ˜¯æ¨¡æ‹Ÿäº†æŒ‰ä¸‹é”®ç›˜çš„F11ï¼Œä½¿æµè§ˆå™¨å…¨å±
    let wscript = new ActiveXObject('WScript.Shell') //eslint-disable-line
    if (wscript != null) {
      wscript.SendKeys('{F11}')
    }
  }
}

// é€€å‡ºå…¨å±
export const exitFullScreen = () => {
  let el = document
  let cfs =
    el.cancelFullScreen ||
    el.mozCancelFullScreen ||
    el.msExitFullscreen ||
    el.webkitExitFullscreen ||
    el.exitFullscreen
  if (cfs) {
    // typeof cfs != "undefined" && cfs
    cfs.call(el)
  } else if (typeof window.ActiveXObject !== 'undefined') {
    // for IEï¼Œè¿™é‡Œå’ŒfullScreenç›¸åŒï¼Œæ¨¡æ‹ŸæŒ‰ä¸‹F11é”®é€€å‡ºå…¨å±
    let wscript = new ActiveXObject('WScript.Shell') //eslint-disable-line
    if (wscript != null) {
      wscript.SendKeys('{F11}')
    }
  }
}
