import { defaultEventConfig, EventBase } from '../../../event/base'
import Door from './index'

class DoorEvent extends EventBase {
  // å®šä¹‰äº‹ä»¶ç±»å‹å - ä»¥ä¾›äº‹ä»¶æ³¨å†Œï¼Œäº‹ä»¶è§¦å‘æ—¶ä½¿ç”¨
  static MODEL_CLICK = 'MODEL_CLICK' // æ¨¡å‹å•å‡»
  static MODEL_DOUBLE_CLICK = 'MODEL_DOUBLE_CLICK' // æ¨¡å‹åŒå‡»
  static ICON_CLICK = 'ICON_CLICK' // å›¾æ ‡å•å‡»
  static ICON_DOUBLE_CLICK = 'ICON_DOUBLE_CLICK' // å›¾æ ‡åŒå‡»

  constructor(mapViewer) {
    super(mapViewer)

    // éœ€è¦è°ƒç”¨æ¥åˆå§‹åŒ–
    this.listen()
  }

  // æ¨¡å‹ç‚¹å‡»äº‹ä»¶çš„å¤„ç†å‡½æ•°
  _modelClickHandler(entity) {
    const modelCallbackConfig = this.getConfig(DoorEvent.MODEL_CLICK)
    if (modelCallbackConfig) {
      const useDefaultAction = modelCallbackConfig.defaultAction
      if (useDefaultAction) {
        Door.modelClickHandler(entity)
      }

      this.dispatch(DoorEvent.MODEL_CLICK, entity)
    }
  }

  // æ¨¡å‹åŒå‡»äº‹ä»¶çš„å¤„ç†å‡½æ•°
  _modelDoubleClickHandler(entity) {
    const modelCallbackConfig = this.getConfig(DoorEvent.MODEL_DOUBLE_CLICK)
    if (modelCallbackConfig) {
      const useDefaultAction = modelCallbackConfig.defaultAction
      if (useDefaultAction) {
        // å°†å®é™…é»˜è®¤çš„äº‹ä»¶è¡Œä¸ºï¼Œäº¤ç»™å…·ä½“æ¨¡å‹ç±»è‡ªå·±å¤„ç†
        Door.modelDoubleClickHandler(entity)
      }

      this.dispatch(DoorEvent.MODEL_DOUBLE_CLICK, entity)
    }
  }

  // å›¾æ ‡ç‚¹å‡»äº‹ä»¶çš„å¤„ç†å‡½æ•°
  _iconClickHandler(entity) {
    const modelCallbackConfig = this.getConfig(DoorEvent.ICON_CLICK)
    if (modelCallbackConfig) {
      const useDefaultAction = modelCallbackConfig?.defaultAction
      if (useDefaultAction) {
        // å°†å®é™…é»˜è®¤çš„äº‹ä»¶è¡Œä¸ºï¼Œäº¤ç»™å…·ä½“æ¨¡å‹ç±»è‡ªå·±å¤„ç†
        Door.iconClickHandler(entity)
      }
      this.dispatch(DoorEvent.ICON_CLICK, entity)
    }
  }

  // å›¾æ ‡åŒå‡»äº‹ä»¶çš„å¤„ç†å‡½æ•°
  _iconDoubleClickHandler(entity) {
    const modelCallbackConfig = this.getConfig(DoorEvent.ICON_DOUBLE_CLICK)
    if (modelCallbackConfig) {
      const useDefaultAction = modelCallbackConfig.defaultAction
      if (useDefaultAction) {
        // å°†å®é™…é»˜è®¤çš„äº‹ä»¶è¡Œä¸ºï¼Œäº¤ç»™å…·ä½“æ¨¡å‹ç±»è‡ªå·±å¤„ç†
        Door.iconDoubleClickHandler(entity)
      }

      this.dispatch(DoorEvent.ICON_DOUBLE_CLICK, entity)
    }
  }

  listen = () => {
    const event = this.mapViewer.event

    // æ¨¡å‹çš„ç‚¹å‡»
    event.onClick(
      Door.model,
      this._modelClickHandler.bind(this),
      {
        compareFunc: (_, gid) => gid.startsWith(Door.model)
      }
    )

    // æ¨¡å‹åŒå‡»
    event.setMouseDoubleClickCallback(
      Door.model,
      this._modelDoubleClickHandler.bind(this),
      {
        compareFunc: (_, gid) => gid.startsWith(Door.model)
      }
    )

    // å›¾æ ‡å•å‡»
    event.onClick(Door.icon, this._iconClickHandler.bind(this), {
      compareFunc: (_, gid) => gid.startsWith(Door.icon)
    })

    // å›¾æ ‡åŒå‡»
    event.setMouseDoubleClickCallback(
      Door.icon,
      this._iconDoubleClickHandler.bind(this),
      {
        compareFunc: (_, gid) => gid.startsWith(Door.icon)
      }
    )
  }

  /**
   * @desc æ¨¡å‹çš„å•å‡» - æ¨¡å‹å•å‡»ï¼Œç›®å‰å°±åšè§†è§’è·³è½¬ï¼Œç„¶åå°†ç‚¹å‡»çš„æ¨¡å‹å±æ€§å›ä¼ ã€‚
   * @param callback
   * @param config
   */
  onClick(callback, config = defaultEventConfig) {
    this.subscribe(DoorEvent.MODEL_CLICK, callback, config)
  }

  offClick(callback) {
    this.unsubscribe(DoorEvent.MODEL_CLICK, callback)
  }

  /**
   * @desc æ¨¡å‹çš„å•å‡» - æ¨¡å‹å•å‡»ï¼Œç›®å‰å°±åšè§†è§’è·³è½¬ï¼Œç„¶åå°†ç‚¹å‡»çš„æ¨¡å‹å±æ€§å›ä¼ ã€‚
   * @param callback
   * @param config
   */
  onDoubleClick(callback, config = defaultEventConfig) {
    this.subscribe(DoorEvent.MODEL_DOUBLE_CLICK, callback, config)
  }

  offDoubleClick(callback) {
    this.unsubscribe(DoorEvent.MODEL_DOUBLE_CLICK, callback)
  }

  /**
   * @desc æ¨¡å‹çš„å•å‡» - æ¨¡å‹å•å‡»ï¼Œç›®å‰å°±åšè§†è§’è·³è½¬ï¼Œç„¶åå°†ç‚¹å‡»çš„æ¨¡å‹å±æ€§å›ä¼ ã€‚
   * @param callback
   * @param config
   */
  onIconClick(callback, config = defaultEventConfig) {
    this.subscribe(DoorEvent.ICON_CLICK, callback, config)
  }

  /**
   * @desc ç§»é™¤å›¾æ ‡å•å‡»
   * @param callback
   */
  offIconClick(callback) {
    this.unsubscribe(DoorEvent.ICON_DOUBLE_CLICK, callback)
  }

  /**
   * @desc æ¨¡å‹çš„å•å‡» - æ¨¡å‹å•å‡»ï¼Œç›®å‰å°±åšè§†è§’è·³è½¬ï¼Œç„¶åå°†ç‚¹å‡»çš„æ¨¡å‹å±æ€§å›ä¼ ã€‚
   * @param callback
   * @param config
   */
  onIconDoubleClick(callback, config = defaultEventConfig) {
    this.subscribe(DoorEvent.ICON_DOUBLE_CLICK, callback, config)
  }

  /**
   * @desc ç§»é™¤å›¾æ ‡åŒå‡»äº‹ä»¶
   * @param callback
   */
  offIconDoubleClick(callback) {
    this.unsubscribe(DoorEvent.ICON_DOUBLE_CLICK, callback)
  }
}

export default DoorEvent
