// import { utils } from '@ty/shared'
import * as utils from '../utils'
import { batchModelOptions, modelOptions } from './options'
import Model from './category'

const { isArray, NOOP, toTypeString } = utils

/**
 * @classdesc æ¨¡å‹å®ä¾‹åŒ–ï¼Œé”€æ¯ã€æ›´æ–°çš„æ§åˆ¶å™¨
 * @class
 */
class ModelController {
  constructor(mapViewer) {
    this.mapViewer = mapViewer
  }

  // æ·»åŠ ä¸€ä¸ª
  addOne = (data, config = modelOptions) => {
    return new Model(this.mapViewer).create(data, config)
  }

  updateOne = (instance, updater = {}) => {
    instance?.update(updater)
  }

  deleteOne = (instance) => {
    instance?.destroy()
  }

  /**
   * æ‰¹é‡æ·»åŠ æ¨¡å‹
   * @param source {Array}
   * @param options {Object}
   */
  addMany = (source, options = batchModelOptions) => {
    const mapViewer = this.mapViewer
    const drawer = mapViewer.drawer

    options = Object.assign(
      {},
      {
        size: 200,

        onSuccess: NOOP,

        onProgress: NOOP
      },
      options
    )

    const { size, onSuccess, onProgress } = options

    if (!isArray(source)) {
      console.error('type source must be an array, got ' + toTypeString(source))
      return
    }

    const start = +new Date()
    const sourceSize = source.length
    const addModel = (startOffset, endOffset = 0) => {
      const sourceSlice = source.slice(startOffset, endOffset)
      if (startOffset > sourceSize - 1) {
        const end = +new Date()

        onProgress &&
        onProgress({
          progress: 100
        })

        console.log(
          `åŠ è½½ ${sourceSize} ä¸ªæ¨¡å‹,å…±è€—æ—¶: ${(end - start) / 1000}s. `
        )

        onSuccess && onSuccess()
        return
      }

      drawer
        .overLayerCreateObjects(sourceSlice)
        .then(() => {
          if (startOffset < sourceSize) {
            onProgress &&
            onProgress({
              progress: Math.floor(
                (Math.pow(10, 4) * (startOffset / sourceSize)) / 100
              )
            })

            addModel(endOffset, endOffset + size)
          }
        })
        // TODO: æœ‰å¯èƒ½è¿™é‡Œçš„é”™è¯¯å›è°ƒéƒ½ä¸æ‰§è¡Œï¼Œè€ƒè™‘çœ‹ï¼Œä½¿ç”¨å®šæ—¶å™¨åœ¨è¿™é‡Œåšå¤±è´¥çš„ç»§ç»­æ·»åŠ 
        .catch((error) => {
          console.error(error)
          // çœ‹çœ‹èƒ½å¦åœ¨å¤±è´¥åé‡æ–°æ·»åŠ .å¦‚æœæ˜¯åœ°å›¾æ–­å¼€,è¿™é‡Œä¹Ÿåšæç¤º
          // addModel(endOffset, endOffset + size)
        })
    }

    addModel(0, size)
  }

  // æ‰¹é‡æ›´æ–°
  // æ›´æ–°çš„é€»è¾‘æ¯”è¾ƒå¤æ‚ï¼Œæ¶‰åŠçš„æ›´æ–°ç±»å‹æ¯”è¾ƒå¤š(æ›´æ–°å¯è§æ€§ï¼Œ)
  /**
   *
   * @param instances {Array<ModelBase>}
   * @param options {Object}
   * @param options.visible {Boolean} ç›®å‰åªè€ƒè™‘æ›´æ–°å¯è§æ€§
   */
  updateMany = (instances, options) => {
    const entityToUpdate = instances.reduce((entities, instance) => {
      entities.push(...instance.getAllEntities().map(entity => ({
        ...entity.data,
        ...options
      })))
      instance.markVisible(options.visible)
      return entities
    }, [])
    // TODO: é€šè¿‡å†…éƒ¨ API è°ƒç”¨ï¼Œè€Œä¸æ˜¯åº•å±‚çš„ SetObjectsVisibleï¼Œ é¿å…å‡ºç°é€ æˆç‚¹å‡»äº‹ä»¶å¤±æ•ˆçš„é—®é¢˜ã€‚
    this.mapViewer.core.view3d.SetObjectsVisible(entityToUpdate)
  }

  // æ‰¹é‡åˆ é™¤
  deleteMany = () => {
  }
}

export default ModelController
