import * as utils from './utils'
const { isString, isPlainObject } = utils

//æ— æ•ˆå‚æ•°
export const InvalidParameter = {
  code: 400,
  msg: 'invalid parameter'
}
//view3dä¸ºåˆå§‹åŒ–
export const View3dUnInitialized = {
  code: 401,
  msg: 'view3d uninitialized '
}

export function logError(errorMsg = 'error') {
  if (isString(errorMsg)) {
    console.error(errorMsg)
  }

  if (isPlainObject(errorMsg)) {
    console.log(errorMsg.msg)
  }
}

export function warn(errorMsg) {
  if (isString(errorMsg)) {
    console.warn(errorMsg)
  }

  if (isPlainObject(errorMsg)) {
    console.warn(errorMsg.msg)
  }
}

export function functionExist(obj, funcName) {
  if (!obj) {
    return
  }

  if (!funcName in obj) {
    logError(`${funcName} dose not exist.`)
    return false
  }

  return true
}
