// import { utils } from '@ty/shared'
import * as utils from '../utils'
import Selector from './selector'
import ModelController from './controller'
import { batchModelOptions, modelOptions } from './options'
import { MODEL_INSTANCE_TYPE } from './category/base'

const { isArray } = utils

/**
 * @classdesc å¯¹æ‰€æœ‰æ¨¡å‹è¿›è¡Œç®¡ç†çš„ Adapter
 * @class
 */
class EntityAdapter {
  constructor(mapViewer) {
    this.entity = {
      // æ¯ä¸ªæ•°æ®åŸå§‹çš„ID
      ids: new Set(),

      // æŸ¥è¯¢è¡¨ï¼Œkeyæ˜¯åŸå§‹æ•°æ®çš„IDï¼Œå€¼æ˜¯åŸå§‹æ•°æ®å¯¹è±¡
      data: new Map()
    }

    this.mapViewer = mapViewer

    this.selector = new Selector(this.entity)

    this.controller = new ModelController(this.mapViewer)
  }

  // è·å–é€‰æ‹©å™¨å®ä¾‹
  getSelector = () => {
    return this.selector
  }

  // è¦æœ‰ä¸€ä¸ªä¸“é—¨å¤„ç†å°†æ•°æ®æ·»åŠ åˆ°åœ°å›¾çš„å®ä¾‹
  getController = () => {
    return this.controller
  }

  getInitialState = (initialState = null) => {
    return Object.assign({}, this.entity, initialState)
  }

  addOne = (data, config = {}) => {
    config = {
      ...modelOptions,
      ...config
    }
    // ID å¯èƒ½æ˜¯ä¸å­˜åœ¨çš„ï¼Œè¿™ä¸ªè¦ç”Ÿæˆéšæœºçš„ IDï¼Œä»¥ä¾¿åç»­å¯ä»¥æŸ¥æ‰¾åˆ°
    const id = data.id
    this.entity.ids.add(data.id)
    // modelInstance ä¸ºæŸä¸ªæ¨¡å‹å¯¹è±¡çš„å®ä¾‹
    const modelInstance = this.getController().addOne(data, config)
    this.entity.data.set(id, modelInstance)

    return modelInstance
  }

  /**
   * æ‰¹é‡åˆ›å»º
   * @param dataList {Array<Object | ModelBase>}
   * @param config {Object}
   */
  addMany = (dataList = [], config = {}) => {
    if (!isArray(dataList)) {
      console.warn('addMany take array  as only argument.')
      return
    }

    config = {
      ...batchModelOptions,
      ...config
    }

    let modelInstanceArray = []

    if (config.instance) {
      for (let item of dataList) {
        // æ‰¹é‡æ·»åŠ æ—¶ï¼Œåˆ›å»ºæ¯ä¸ªæ¨¡å‹çš„å®ä¾‹ï¼Œä½†æ˜¯éƒ½ä¸ä¸Šå›¾ï¼Œå†åç»­çš„é€»è¾‘é‡Œæ‰¹é‡ä¸Šå›¾
        const modelInstance = this.addOne(item, {
          ...modelOptions,
          onMap: false
        })
        modelInstanceArray.push(modelInstance)
      }

    } else {
      modelInstanceArray = dataList
    }

    // è¿™é‡Œç”¨ç»Ÿä¸€çš„æ‰¹é‡åŠ è½½
    if (config.onMap) {
      const { selectMapEntity } = this.getSelector()
      let sourceEntities = selectMapEntity(modelInstanceArray)
      this.controller.addMany(sourceEntities, {
        ...config,
        onSuccess: function() {
          if (!config.instance) {
            // å¦‚æœæ˜¯ä¸éœ€è¦å®ä¾‹åŒ–çš„å®ä¾‹ï¼Œè¿™é‡Œæ›´æ”¹ä»–ä»¬çš„ visibleï¼ŒisCreated çŠ¶æ€
            for (let instance of dataList) {
              if (instance.$$typeof === MODEL_INSTANCE_TYPE) {
                instance.markVisible(true)
                instance.markCreated(true)
              }
            }
          }
        }
      })
    }

    return this
  }

  /**
   * setAll åœ¨æ•´ä¸ªåº”ç”¨ç”Ÿå‘½å‘¨æœŸä¸­åº”è¯¥åªæ‰§è¡Œä¸€æ¬¡ã€‚åç»­çš„æ“ä½œå¯ä»¥é€šè¿‡addManyï¼ŒaddOneæ–°å¢æ•°æ®ã€‚
   * ä¸ºçš„æ˜¯åŒºåˆ†é¦–æ¬¡åŠ è½½æ•°æ®å’Œåç»­çš„æ•°æ®å¢å‡ã€‚
   * @param dataList
   * @param config {Object}
   */
  setAll = (dataList = [], config = {}) => {
    if (!isArray(dataList)) {
      console.warn('setAll take array  as only argument.')
      return
    }

    const options = {
      ...batchModelOptions,
      ...config,
      isFirst: true
    }

    //  æ¯æ¬¡ setAll éƒ½æ˜¯é‡æ–°æ·»åŠ å…ƒç´ 
    this.removeAll()

    this.addMany(dataList, options)

    return this
  }

  removeOne = (id) => {
    if (!id) {
      return
    }
    // entityé‡Œæœ‰ä¸Šå›¾çš„è®¾å¤‡ï¼Œåº”è¯¥ä»åœ°å›¾ä¸Šç§»é™¤
    const instance = this.getSelector().selectEntityById(id)
    this.getController().deleteOne(instance)

    // å†æŠŠå­˜èµ·æ¥çš„æ•°æ®ç§»é™¤
    this.entity.ids.delete(id)
    this.entity.data.delete(id)

    return this
  }

  /**
   * åˆ é™¤å¤šä¸ª
   * @param ids
   */
  removeMany = (ids) => {
    if (!isArray(ids)) {
      console.warn('removeMany take array  as only argument.')
      return
    }

    // å…ˆåšå•ä¸ªåˆ é™¤
    for (let id of ids) {
      this.removeOne(id)
    }

    return this
  }

  /**
   * åˆ é™¤æ‰€æœ‰
   * @returns {EntityAdapter}
   */
  removeAll = () => {
    this.removeMany(Array.from(this.entity.ids))

    this.entity.ids.clear()
    this.entity.data.clear()

    return this
  }

  /**
   * dataå¦‚æœä¸å­˜åœ¨ï¼Œ åˆ™æ’å…¥ï¼Œå¦‚æœå­˜åœ¨åˆ™æ›´æ–°
   * @param data
   */
  upsertOne = (data) => {
    if (this.getSelector().selectDataById(data.id)) {
      this.updateOne(data)
    } else {
      this.addOne(data)
    }

    return this
  }

  upsertMany = (dataList) => {
    for (let item of dataList) {
      this.upsertOne(item)
    }

    return this
  }

  updateOne = (
    updater = {
      data: null,
      // æ›´æ–°æ¨¡å‹çš„é…ç½®é€‰é¡¹ï¼Œæ¯”å¦‚ï¼Œå›¾æ ‡æ˜¾ç¤ºå’Œéšè—ï¼Œæ¨¡å‹æ˜¾ç¤ºå’Œéšè—ã€‚
      config: {},
      changes: null
    }
  ) => {
    let { data, config } = updater
    if (!data || !data.id) {
      return
    }

    config = {
      ...modelOptions,
      ...config
    }

    const instance = this.getSelector().selectEntityById(data.id)
    if (instance) {
      // å¦‚æœæ˜¯åŒä¸€ç±»è®¾å¤‡ï¼Œåšæ›´æ–°çš„é€»è¾‘ï¼Œ TODOï¼š å¦‚æœæ˜¯é€šç”¨çš„æ¨¡å‹ï¼Œæ¯”å¦‚å¹¿å‘Šç‰ˆï¼Œè¿™é‡Œåˆ¤å®šçš„é€»è¾‘å°†ä¼šä¸åŒã€‚
      if (instance.data.category_id === data.category_id) {
        this.getController().updateOne(instance, updater)

        // å¦‚æœæ”¹å˜äº†è®¾å¤‡ç±»åˆ«ï¼Œåˆ›å»ºæ–°ç±»åˆ«çš„è®¾å¤‡å®ä¾‹ï¼Œç„¶åæ›¿æ¢ entity
      } else {
        this.removeOne(data.id)
        return this.addOne(data, config)
      }
      // å¦‚æœæ˜¯æ²¡æœ‰ä¿å­˜è¿‡çš„æ¨¡å‹å®ä½“ã€‚ä¸‹é¢å»åˆ›å»º
    } else {
      console.warn(
        `Can not update for Id: ${data.id}, because it's instance don't exist.`
      )
    }
  }

  // TODO: æ‰¹é‡æ›´æ–°
  updateMany = (updater = []) => {
    if (!isArray(updater)) {
      console.warn('updateMany take Array as only argument.')
      return
    }
    // ç›®å‰å…ˆåšå•ä¸ªçš„æ›´æ–°
    for (let item of updater) {
      this.updateOne(item)
    }

    return this
  }

  /**
   * @desc è®¾ç½®æ¨¡å‹çš„æ˜¾ç¤ºå’Œéšè—
   * @param options {Object}
   * @param options.visible {Boolean}
   * @param options.buildId {String}
   * @param options.floorId {String}
   * @param options.outdoor {String}
   */
  setVisible(options = {}) {
    const defaultOptions = {
      visible: true, // é»˜è®¤æ˜¾ç¤º
      outdoor: true, // é»˜è®¤åŒ…å«å®¤å¤–
      buildId: '', // é»˜è®¤ä¸åŒ…å«å®¤å†…çš„è®¾å¤‡
      floorId: ''
    }

    // è€ƒè™‘å¦‚ä½•æœ€å°åŒ–å¯¹åœ°å›¾çš„æ“ä½œï¼Œæ¯”å¦‚ï¼Œä¸é‡å¤æ·»åŠ å·²ç»æ˜¯ visible çŠ¶æ€çš„è®¾å¤‡ã€‚
    //  è¿˜æœ‰ï¼Œè€ƒè™‘åˆ°æœ‰äº›æ¨¡å‹æ˜¯å¦å·²ç»å®Œæˆä¸Šå›¾ã€‚
    //  æœªä¸Šå›¾çš„è®¾å¤‡ï¼ŒsetVisible æ„å‘³ç€è¦ createï¼Œ
    //  å·²ä¸Šå›¾ï¼Œä½†æ˜¯ visible = falseï¼Œ çš„è®¾å¤‡ï¼Œæ›´æ–°å¯è§æ€§

    const { outdoor, buildId, floorId } = Object.assign(
      {},
      defaultOptions,
      options
    )

    const selector = this.getSelector()

    // ç¬¦åˆæ¡ä»¶ï¼Œæœ¬æ¬¡åº”è¯¥æ˜¾ç¤º/åˆ›å»ºçš„æ¨¡å‹
    let instanceToShow = []

    // é€‰æ‹©æ•´æ ‹æ¥¼
    if (buildId && !floorId) {
      instanceToShow = selector.filterEntityByBuilding(buildId)

      // é€‰æ‹©æŸæ ‹æ¥¼æŸä¸€å±‚
    } else if (buildId && floorId) {
      instanceToShow = selector.filterEntityByFloor(buildId, floorId)
    }

    // æ˜¯å¦åŒ…å«å®¤å¤–
    if (outdoor) {
      instanceToShow = instanceToShow.concat(selector.filterOutDoorEntities())
    }

    // è¿‡æ»¤å‡ºå·²ä¸Šå›¾çš„ï¼Œ å³ data.onMap = true çš„è®¾å¤‡ï¼Œè¿™é‡Œçš„æ˜¯å¦ä¸Šå›¾ï¼ŒæŒ‡çš„æ˜¯åœ¨æ•°æ®ä¸­æ˜¯å¦å®Œæˆè¿‡ä¸Šå›¾ï¼Œè€Œä¸æ˜¯æ˜¯å¦æ·»åŠ åˆ°åœ°å›¾ä¸Š
    instanceToShow = instanceToShow.filter((instance) => instance.data.on_map)

    let instanceToCreate = [] // å¦‚æœæ²¡æœ‰ä¸Šè¿‡å›¾çš„å®ä¾‹ï¼Œéœ€è¦ä¸Šå›¾
    let instanceToUpdate = [] // å·²ç»ä¸Šè¿‡å›¾çš„ï¼Œæ ¹æ® visible çš„å˜åŒ–åšæ›´æ–°
    let instanceToHide = []

    // è·å–æ‰€æœ‰å½“å‰ visible ä¸º true çš„å®ä¾‹, è€Œå½“å‰ visible çš„å®ä¾‹ï¼Œä¸€å®šæ˜¯ created çš„ã€‚
    const currentVisibleInstances = this.getVisible()

    // æ‰¾å‡ºä¸Šä¸€æ¬¡æ˜¾ç¤ºçš„ï¼Œæœ¬æ¬¡éšè—çš„
    for (let currentInstance of currentVisibleInstances) {
      const shouldHide = instanceToShow.find(instance => instance.id === currentInstance.id)
      if (!shouldHide) {
        instanceToHide.push(currentInstance)
      }
    }

    // æ‰¾å‡ºæœ¬æ¬¡å·²åˆ›å»ºçš„ï¼Œéœ€è¦æ˜¾ç¤ºçš„ï¼Œ
    for (let currentInstance of instanceToShow) {
      if (currentInstance.created) {
        const shouldHide = currentVisibleInstances.find(instance => instance.id === currentInstance.id)
        if (!shouldHide) {
          instanceToUpdate.push(currentInstance)
        }
      }
    }

    // TODO: é€šè¿‡æ¯”è¾ƒå½“å‰éœ€è¦æ›´æ–°çš„å®ä¾‹å’Œå·²ç»æ˜¯ visible çŠ¶æ€çš„å®ä¾‹æ¥å†³å®šï¼Œå“ªäº›æ˜¯éœ€è¦æ–°å»ºçš„ï¼Œå“ªäº›æ˜¯éœ€è¦éšè—çš„ã€‚

    // æœªåˆ›å»ºè¿‡çš„ï¼Œå»åˆ›å»º
    instanceToShow.forEach(instance => {
      // æ²¡æœ‰æ·»åŠ åˆ°åœ°å›¾çš„å®ä¾‹ï¼Œæ·»åŠ åˆ°éœ€è¦ä¸Šå›¾çš„é˜Ÿåˆ—é‡Œ
      if (!instance.created) {
        instanceToCreate.push(instance)
      }
    })

    // è®¾å¤‡å®ä¾‹ï¼Œæ²¡æœ‰ä¸Šè¿‡å›¾çš„ï¼Œæ‰¹é‡åˆ›å»º
    // å› ä¸ºæ¨¡å‹çš„å®ä¾‹å·²ç»åˆå§‹åŒ–ï¼Œæ‰€ä»¥è¿™é‡Œè¦è€ƒè™‘çš„æ˜¯ï¼Œæ€ä¹ˆä¸é‡å¤åˆå§‹åŒ–å®ä¾‹ï¼Œ
    // è€Œåªåšä¸Šå›¾æ“ä½œï¼Œä¸”èƒ½æ›´æ–°å¯¹åº”å±æ€§
    if (instanceToCreate && instanceToCreate.length > 0) {
      this.addMany(instanceToCreate, {
        // ç¡®ä¿æ¨¡å‹ä¸Šå›¾
        onMap: true,
        // ä¸éœ€è¦é‡å¤å®ä¾‹åŒ–å®ä¾‹
        instance: false
      })
    }

    // éœ€è¦æ˜¾ç¤ºçš„
    if (instanceToUpdate && instanceToUpdate.length > 0) {
      this.getController().updateMany(instanceToUpdate, {
        visible: true
      })
    }


    // éœ€è¦éšè—çš„
    if (instanceToHide && instanceToHide.length > 0) {
      this.getController().updateMany(instanceToHide, {
        visible: false
      })
    }

    return this
  }

  /**
   * è¿”å›å½“å‰æ‰€æœ‰ä¸Šå›¾çš„è®¾å¤‡çš„ç¤ºä¾‹
   */
  getVisible() {
    const selector = this.getSelector()
    return selector.selectEntities().filter((instance) => instance.visible)
  }
}

export default EntityAdapter
