// import { utils } from '@ty/shared'
import * as utils from '../utils'
const { NOOP } = utils

/**
 * @classdesc æ¨¡å‹é€‰æ‹©å™¨ç±»
 * @class
 */
class Selector {
  constructor(entity, mapViewer) {
    this.entity = entity
    this.mapViewer = mapViewer
  }

  /**
   * è¿”å›æ‰€æœ‰çš„æ¨¡å‹çš„IDç»„æˆçš„æ•°ç»„
   *
   */
  selectIds = () => {
    return Array.from(this.entity.ids)
  }

  /**
   * è¿”å›æ‰€æœ‰çš„æ¨¡å‹å¯¹è±¡å®ä¾‹
   *
   */
  selectEntities = () => {
    return Array.from(this.entity.data.values())
  }

  /**
   * è¿”å›æ‰€æœ‰åŸå§‹çš„æ•°æ®
   *
   */
  selectAll = () => {
    const ids = this.selectIds()
    return ids.reduce((ret, id) => {
      const data = this.selectDataById(id)
      if (data) {
        ret.push(data)
      }
      return ret
    }, [])
  }

  /**
   * è¿”å›å½“å‰æ¨¡å‹çš„æ•°é‡
   *
   */
  selectTotal = () => {
    return this.selectIds().length
  }

  /**
   * æŸ¥æ‰¾æŸä¸ªidå¯¹åº”çš„åŸå§‹æ•°æ®
   * @param id
   *
   */
  selectDataById = (id) => {
    const entityInstance = this.entity.data.get(id)
    if (!entityInstance) {
      console.warn(`cannot find entity for id: ${id}.`)
      return null
    }
    return entityInstance.getState()
  }

  /**
   * æŸ¥æ‰¾æŸä¸ªIDå¯¹åº”çš„æ¨¡å‹å®ä¾‹
   * @param id
   *
   */
  selectEntityById = (id) => {
    const entityInstance = this.entity.data.get(id)
    if (!entityInstance) {
      console.warn(`cannot find entity for id: ${id}.`)
      return null
    }
    return entityInstance
  }

  /**
   * åˆ†ç±»æŸ¥æ‰¾åŸå§‹æ•°æ®
   * @param categoryId
   * @param key
   *
   */
  selectByCategory = (categoryId, key = 'category_id') => {
    return this.selectAll().filter((item) => item[key] === categoryId)
  }

  /**
   * åˆ†ç±»æŸ¥æ‰¾æ¨¡å‹å®ä¾‹
   * @param categoryId
   * @param key
   *
   */
  selectEntitiesByCategory = (categoryId, key = 'category') => {
    return this.selectEntities().filter((item) => item[key] === categoryId)
  }

  /**
   * æ ¹æ®IDè¿”å›åœ°å›¾å®ä½“æ•°æ®é›†åˆ
   * @param id
   */
  selectMapEntityById = (id) => {
    return this.selectEntityById(id).getEntityData()
  }

  /**
   * æ ¹æ®entities è¿”å›ç›¸åº”çš„åœ°å›¾å®ä½“æ•°æ®
   * @param instances
   */
  selectMapEntity = (instances) => {
    return instances.reduce((retArray, instance) => {
      retArray.push(...instance.getEntityData())
      return retArray
    }, [])
  }

  // é€šç”¨çš„è¿‡æ»¤æ•°æ®çš„æ–¹æ³•
  filter = (filterFunc = NOOP) => {
    return this.selectAll().filter(filterFunc)
  }

  // é€šç”¨çš„è¿‡æ»¤æ¨¡å‹å®ä¾‹çš„æ–¹æ³•
  filterEntities = (filterFunc = NOOP) => {
    return this.selectEntities().filter(filterFunc)
  }

  // æ‰€æœ‰å®¤å¤–çš„æ•°æ®
  filterOutDoor = () => {
    return this.filter((item) => !item.indoor)
  }

  // æ‰€æœ‰å®¤å¤–çš„æ¨¡å‹å®ä¾‹
  filterOutDoorEntities = () => {
    return this.filterEntities((entity) => !entity.data.indoor)
  }

  // æŸæ ‹å»ºç­‘çš„æ•°æ®
  filterByBuilding = (buildId) => {
    return this.filter((data) => data.build_id === buildId)
  }

  // æŸæ ‹æ¥¼çš„æ‰€æœ‰æ¨¡å‹å¯¹è±¡
  filterEntityByBuilding = (buildId) => {
    return this.filterEntities((instance) => instance.data.build_id === buildId)
  }

  // æŸæ¥¼å±‚çš„æ‰€æœ‰æ•°æ®
  filterByFloor = (buildId, floorId) => {
    return this.filter(
      (data) =>
        data.indoor && data.build_id === buildId && data.floor_id === floorId
    )
  }

  // æŸæ¥¼å±‚çš„æ‰€æœ‰æ¨¡å‹å®ä¾‹
  filterEntityByFloor = (buildId, floorId) => {
    return this.filterEntities((instance) => {
      return (
        instance.data.indoor &&
        instance.data.build_id === buildId &&
        instance.data.floor_id === floorId
      )
    })
  }
}

export default Selector
