class EventsBus {
  constructor() {
    this.events = new Map()
  }

  _add(key, fn, isOnce) {
    const m = this.events.get(key)

    const fnMap = m ? m : this.events.set(key, new Map()).get(key)

    fnMap.set(key, (...arg) => {
      fn(...arg)
      isOnce && this.off(key, fn)
    })
  }

  /**
   * æ³¨å†Œä¸€ä¸ªäº‹ä»¶
   * @param key äº‹ä»¶å¯¹åº”çš„keyï¼Œåº”è¯¥å”¯ä¸€
   * @param fn äº‹ä»¶è§¦å‘æ—¶çš„å›è°ƒ
   * @param args éœ€è¦ä¼ ç»™å›è°ƒçš„å‚æ•°
   */
  on(key, fn) {
    if (!fn) {
      console.error('No callback for key: ', key)
      return
    }
    this._add(key, fn, false)
  }

  off(key, fn) {
    const fnMap = this.events.get(key)
    if (fnMap) {
      fnMap.delete(fn)
    }
  }

  /**
   * è§¦å‘å¯¹åº”çš„äº‹ä»¶å›è°ƒ
   * @param key
   * @param args
   */
  dispatch(key, ...args) {
    const fnMap = this.events.get(key)
    if (!fnMap) {
      console.error('No callback for key: ', key)
      return
    }
    for (let [, cb] of fnMap.entries()) {
      cb(...args)
    }
  }

  once(key, fn) {
    this._add(key, fn, true)
  }
}

export default new EventsBus()
