import * as utils from '../../utils'

const { createUUID } = utils

export const MODEL_INSTANCE_TYPE = 'MODEL_INSTANCE_TYPE'

// æ¨¡å‹å¯¹è±¡åŸºç±»
class ModelBase {
  static CATEGORY = 'MODEL'

  constructor(mapViewer, data, config) {
    // å†…éƒ¨å®ä¾‹çš„ç±»å‹ï¼Œç”¨æ¥éœ€è¦å®ä¾‹åŒ–åçš„ç±»å®ä¾‹å’Œæ™®é€šå¯¹è±¡
    this.$$typeof = MODEL_INSTANCE_TYPE
    this.config = config // åˆ›å»ºæ—¶ï¼Œè‡ªå®šä¹‰çš„é…ç½®é¡¹
    this.data = null // ä¸æ¨¡å‹å…³è”çš„å…·ä½“æ•°æ®
    this.id = null // æ¨¡å‹çš„å”¯ä¸€æ ‡è¯†
    // ç”¨ Map ä¿å­˜ä¸è¿™ä¸ªæ¨¡å‹ç±»å…³è”çš„æ‰€æœ‰å®ä½“
    this.entity = new Map() // æ¨¡å‹ä¸Šå›¾åçš„åœ°å›¾å®ä½“å±æ€§ map
    this.type = data.category_id
    this.typeName = 'æ¨¡å‹åŸºç±»'

    // å®ä¾‹æ¨¡å‹æ˜¯å¦å¯è§
    this.visible = false

    // æ˜¯å¦æ·»åŠ åˆ°åœ°å›¾
    this.created = false

    this.mapViewer = mapViewer

    this.setData(data)
  }

  _concatGid(prefix = createUUID(), id = createUUID()) {
    return `${prefix}_${id}`
  }

  setState(key, value) {
    this[key] = value
    // åˆå¹¶å±æ€§æ›´æ–°
  }

  // è¿”å›åŸå§‹çš„æ•°æ®
  getData() {
    return this.data
  }

  setData(data) {
    if (data) {
      this.data = data
      this.id = data.id
    }
  }

  // è¿”å›å®ä¾‹çš„ entity Map
  getEntity(key) {
    if (key) {
      return this.entity.get(key)
    }
    return this.entity
  }

  // è¿”å›å½“å‰å®ä¾‹æ‰€æœ‰å…³è”çš„ entity
  getAllEntities() {
    return Array.from(this.entity.values())
  }

  /**
   * è¿”å›å®ä¾‹æ‰€æœ‰çš„åœ°å›¾å®ä¾‹æ•°æ®åˆ—è¡¨
   * @returns {unknown[]}
   */
  getEntityData() {
    return Array.from(this.entity.values()).map((v) => v.data)
  }

  /**
   * ä¿å­˜ä¸æ¨¡å‹å…³è”çš„å®ä½“å±æ€§
   * @param key ä¸åŒçš„å®ä½“å¯ä»¥é€šè¿‡ä¸åŒçš„ key æ¥åŒºåˆ†ï¼Œ
   *            å®šä¹‰å¦‚ä¸‹ï¼š
   *                æ¨¡å‹ï¼š model
   *                å›¾æ ‡ï¼š icon
   *                å¯è§†åŒºåŸŸï¼š area - ç›‘æ§å…·æœ‰
   *                å…¶å®ƒï¼š å¾…è¡¥å……
   * @param props æ¯ä¸€ç±»å®ä½“çš„æ•°æ®æ ¼å¼å®šä¹‰å¦‚ä¸‹ï¼š
   *              {
   *                gid: entity ä¸Šå›¾åçš„ gid
   *                type: entity çš„ç±»å‹
   *                data: å®é™…ä¸Šå›¾ååœ°å›¾è¿”å›çš„entityæ•°æ®ï¼Œå¯ä»¥ç›´æ¥ç”¨æ¥ä¸Šå›¾
   *                visible: è¡¨æ˜å½“å‰å®ä½“æ˜¯å¦ç»˜åˆ¶ä¸Šå›¾äº†
   *              }
   */
  setEntity(key, props) {
    this.entity.set(key, {
      gid: props.gid || props.attr.id,
      type: props.type,
      data: props,
      visible: props.visible
    })
  }

  deleteEntity(key) {
    this.entity.delete(key)
  }

  getModel() {
    return this.entity.get('model')
  }

  getIcon() {
    return this.entity.get('icon')
  }

  setModel(props) {
    this.setEntity('model', props)
  }

  setIcon(props) {
    this.setEntity('icon', props)
  }

  /**
   * å®šä¹‰å›¾æ ‡çš„ä½ç½®ï¼Œæ€»æ˜¯åœ¨æ¨¡å‹ä½ç½®ä¸Šæ–¹å›ºå®šè·ç¦»å¤„
   * @param location
   */
  getIconLocation(location) {
    if (!location) {
      return null
    }
    return {
      ...location,
      z: location.z + 20
    }
  }

  /**
   * è®¾ç½®æ•´ä½“å®ä¾‹çš„æ‰€æœ‰å®ä½“çš„å¯è§æ€§ã€‚éœ€è¦å„ä¸ªå®ä¾‹è‡ªå·±å®ç°
   * åªè¦ æ¨¡å‹ å®Œæˆä¸Šå›¾ï¼Œ æ¨¡å‹å®ä¾‹çš„ visible å±æ€§å³ä¸º trueã€‚
   * @abstract
   */
  setVisible(visible) {
    const drawer = this.mapViewer.drawer

    // ä¸é‡å¤è®¾ç½®å¯è§æ€§
    if (visible !== this.visible) {
      for (let entity of this.entity.values()) {
        drawer.updateObjectVisible(entity.gid, visible)
      }

      this.markVisible(visible)
    }
  }

  // æ ‡è®°å®ä¾‹çš„å¯è§æ€§
  markVisible(visible = false) {
    this.visible = visible
  }

  /**
   * æ ‡è®°æ˜¯å¦åˆ›å»º
   */
  markCreated(created = false) {
    this.created = created
  }

  clearEntity() {
    this.entity.clear()
  }

  /**
   * å®šä½
   * @abstract
   */
  flyTo(config) {
    if (this.data.center) {
      const center = this.data.center
      // center ä¸Šè¿˜æœ‰ä¸ª distance å±æ€§ï¼Œç›®å‰è¿™ä¼šå½±å“åˆ° flyToPosition çš„æ•ˆæœ
      const location = {
        x: center.x,
        y: center.y,
        z: center.z,
        pitch: center.pitch,
        yaw: center.yaw,
        roll: center.roll
      }
      this.mapViewer.camera.flyToPositionByOptions({
        position: location,
        duration: config.duration
      })
    }

    // TODO: è¦åŠ ä¸ª configï¼Œå…è®¸æ˜¯å¦åœ¨ flyTo é‡Œåšåˆ†å±‚
    //  åˆ†å±‚çš„æ—¶å€™ï¼Œå…¶å®ä¹Ÿéœ€è¦åšä¸ªåˆ¤æ–­ï¼Œå¦‚æœå½“å‰æ¥¼å±‚å°±æ˜¯ç›®æ ‡æ¥¼å±‚ï¼Œå°±ä¸ç”¨å¤„ç†
    //  å¦‚æœæ˜¯å®¤å†… - å»åšåˆ†å±‚(å»ºç­‘çš„åˆ†å±‚å’Œæ¨¡å‹çš„åˆ†å±‚)
    //  è¦è€ƒè™‘çš„é—®é¢˜åœ¨äºã€‚è¿™é‡Œåšäº†åˆ†å±‚ã€‚é‚£å…³é—­åˆ†å±‚çš„èƒ½åŠ›åº”è¯¥æ˜¯ build æ¨¡å—å¤„ç†çš„,
    //  ç‚¹ä½å®šä½çš„åˆ†å±‚ï¼Œå¯ä»¥é…ç½®æ˜¯å¦å¯ç”¨
    if (this.data.indoor) {
      const buildId = this.data.build_id
      const floorId = this.data.floor_id

      // æ¨¡å‹çš„åˆ†å±‚ã€‚å§”æ‰˜ç»™ ModelManage æ¥å¤„ç†
      // æš‚æ—¶å°±åšå•ä¸ªè®¾å¤‡çš„æ˜¾ç¤ºå’Œéšè—ï¼Œå› ä¸ºæ›´ç®€å•ä¸€ä¸‹
      const model = this.mapViewer.model
      const adapter = model.getAdapter()
      adapter.setVisible(buildId, floorId)
    }
  }

  /**
   * æ¨¡å‹é«˜äº®
   */
  highlight() {
    const entity = this.getModel()
    const mapViewer = this.mapViewer
    this.clearHighlight().then(() => {
      if (entity) {
        // ç›®å‰åªé«˜äº®æ¨¡å‹å³å¯
        mapViewer.drawer.setObjectHighlight(entity?.gid)
      }
    })

    return this
  }

  /**
   * æ¸…é™¤é«˜äº®ç»Ÿä¸€åœ¨ base é‡Œå¤„ç†å³å¯ï¼Œç›®å‰åªæœ‰è¿™ä¸ªæ–¹æ³•æ¥å®ç°
   */
  clearHighlight() {
    const mapViewer = this.mapViewer
    const drawer = mapViewer.drawer
    return drawer.clearHighlight()
  }

  /**
   * æ ¹æ®é…ç½®æ•°æ®.æ¥ç”Ÿæˆæ¨¡å‹, è¿™é‡Œçš„ entity éœ€è¦å¤–ç•Œç›´æ¥ä¼ å…¥,
   * æ„å‘³ç€ï¼Œè¿™é‡Œçš„ create æ˜¯æ›´é€šç”¨çš„åˆ›å»ºæ¨¡å‹çš„æ–¹æ³•
   * @abstract
   */
  create() {
    const self = this
    const config = this.config
    const data = this.data || {} // è¿™é‡Œçš„ data éœ€è¦é™„åŠ  æ¥¼å±‚ä¿¡æ¯ï¼Œæ‰èƒ½æ»¡è¶³åç»­çš„é€»è¾‘
    const drawer = this.mapViewer.drawer
    const event = this.mapViewer.event

    const modelEntity = JSON.parse(JSON.stringify(data))

    const onCreateSuccessHandler = (attr, shouldUpdateEntity) => {
      if (shouldUpdateEntity) {
        self.setModel(data)
      }

      self.markCreated(true)
      self.markVisible(true)

      config.onSuccess(attr)
    }

    if (config.draw) {
      event.setMousePositionCallback(location => {
        modelEntity.location = location
        drawer.overLayerCreateObject(modelEntity).then(attr => {
          onCreateSuccessHandler(attr, true)
        })
      })
    } else {
      // è¿™é‡Œåšä¸€æ¬¡ setModel æ˜¯ä¸ºäº†æŠŠ entity ç¼“å­˜èµ·æ¥ï¼Œæ–¹ä¾¿åç»­é€»è¾‘ä¸Šç»Ÿä¸€åšæ‰¹é‡ä¸Šå›¾
      self.setModel(modelEntity)
      if (config.onMap) {
        drawer.overLayerCreateObject(modelEntity).then(attr => {
          onCreateSuccessHandler(attr, false)
        })
      }
    }
  }

  /**
   * å±æ€§çš„æ›´æ–°ï¼ŒåŒ…æ‹¬ä½ç½®ï¼Œé…ç½®é¡¹ï¼Œæ•°æ®
   * modelInstance.update({
   *   data: cameraProps,
   *   // ä½ç½®å˜åŒ–äº†
   *   changes: newCameraProps.options.location
   * })
   * @desc æ›´æ–°æ¨¡å‹å±æ€§å’Œä½ç½®
   * @param {Object} updater
   * @param {Object} updater.changes  ç‰¹æŒ‡æ¨¡å‹ä½ç½®çš„æ›´æ–°
   * @param {Object} updater.data ç‰¹æŒ‡ä¸æ¨¡å‹å…³è”çš„æ•°æ®æ›´æ–°
   */
  update(updater = {}) {
    const { config, changes, data } = updater
    const drawer = this.mapViewer.drawer

    // TODO:æœ‰é…ç½®é¡¹çš„æ›´æ–°
    if (config) {
      this.setState('config', config)
    }

    // æ•°æ®å±æ€§æ›´æ–°
    if (data) {
      this.setState('data', data)
    }

    // changes åŸºæœ¬é’ˆå¯¹åœ°å›¾æ¨¡å‹ä½ç½®çš„æ›´æ–°, TODOï¼šè¿™é‡Œè¿˜éœ€è¦è€ƒè™‘ï¼Œé€šç”¨æ¨¡å‹çš„æ›´æ–°ï¼Œä¸åªæœ‰ location çš„å˜åŒ–ï¼Œå¯èƒ½è¿˜æœ‰å…¶å®ƒå±æ€§ã€‚
    if (changes) {
      let modelEntity = this.getModel()
      let iconEntity = this.getIcon()
      if (iconEntity) {
        iconEntity.data.location = this.getIconLocation(changes)
        drawer.overLayerUpdateObject(iconEntity.data)
      }
      modelEntity.data.location = changes
      drawer.overLayerUpdateObject(modelEntity.data)
    }
  }

  /**
   * @desc åˆ é™¤æ¨¡å‹å®ä¾‹
   */
  destroy() {
    const entity = this.entity
    const drawer = this.mapViewer.drawer
    for (let [key, props] of entity.entries()) {
      drawer.remove.one(props.data)
    }

    this.clearEntity()
  }

  // TODOï¼šæ›´æ–°ä¸åŒçš„å›¾æ ‡ï¼Œä»¥è¡¨ç¤ºè®¾å¤‡ä¸åŒçš„çŠ¶æ€1
}

export default ModelBase
