  mapkey: '',
  manual: false, // æ˜¯å¦è‡ªåŠ¨åˆ›å»ºåœ°å›¾ï¼Œå…è®¸å»¶è¿Ÿåˆå§‹åŒ–åœ°å›¾
  loading: true, //æ˜¯å¦å±•ç¤ºåˆå§‹ loading çŠ¶æ€
  northControl: false, // åœ°å›¾æŒ‡åŒ—é’ˆæ§ä»¶æ˜¯å¦æ˜¾ç¤º
  enableKeyboard: true, // æ˜¯å¦å¯ç”¨é”®ç›˜
  enableMouse: true, // æ˜¯å¦å¯ç”¨é¼ æ ‡
  defaultLocate: true, // æ˜¯å¦å…è®¸é»˜è®¤å®šä½

  // æ˜¯å¦åœ¨åŠ è½½åœ°å›¾æˆåŠŸåï¼Œç«‹å³è®¾ç½®ä¸€æ¬¡åœ°å›¾åˆ†è¾¨ç‡
  defaultSetResolution: false,

