class MapFind {
  constructor(view3d, drawCtl) {
    this._view3d = view3d
    this._drawCtl = drawCtl
  }

  one(gid) {
    return this._drawCtl.findObjectById(gid)
  }

  /**
   * æ ¹æ®ç»™å®šå‰ç¼€æŸ¥æ‰¾å¯¹è±¡
   * @param prefix {string | undefined}
   *
   */
  all(prefix = '*') {
    return this._drawCtl.findObjectByPrefix(prefix)
  }
}

export default MapFind
