export function isEmpty(value) {
  return (
    value === undefined ||
    value === null ||
    value === '' ||
    value === 'undefined'
  )
}

/**
 * @function
 * åˆ¤æ–­æ˜¯å¦ä¸ºç©º ä¸ºç©ºè¿”å› false ä¸ä¸ºç©ºè¿”å›true
 * @param {object} value å€¼
 * @returns è¿”å›å½“å‰å€¼æ˜¯å¦ä¸ºä¸ç©º
 */
export function isNotEmpty(value) {
  return value === undefined ||
  value === null ||
  value === '' ||
  value === 'undefined'
    ? false
    : true
}

/**
 * åˆ¤æ–­æ˜¯å¦ä¸ºæ•°ç»„
 * @param {Array} arr æ•°ç»„
 */
export function isArray(arr) {
  if (typeof Array.isArray === 'export function') {
    return Array.isArray(arr)
  } else {
    return Object.prototype.toString.call(arr) === '[object Array]'
  }
}

