let treeUtil = {
  treeDfs(tree, func) {
    tree.forEach((node) => {
      func(node)
      node.children && treeUtil.treeDfs(node.children, func)
    })
  },
  /**
   * @description æ ‘è½¬åˆ—è¡¨
   */
  treeToList: (arr) => {
    let res = [] // ç”¨äºå­˜å‚¨é€’å½’ç»“æœï¼ˆæ‰å¹³æ•°æ®ï¼‰
    // é€’å½’å‡½æ•°
    let fn = (source) => {
      source.forEach((el) => {
        res.push(el)
        el.children && el.children.length > 0 ? fn(el.children) : '' // å­çº§é€’å½’
      })
    }
    fn(arr)
    return res
  },

  /**
   * @description åˆ—è¡¨è½¬æ ‘
   */
  list2Tree: (arr) => {
    // åŒå¾ªç¯
    arr.forEach((e) => {
      // çˆ¶å¾ªç¯
      arr.forEach((y) => {
        // å­å¾ªç¯
        if (y.pid === e.id) {
          // å­å–parentIdå’Œçˆ¶idåšå¯¹æ¯”
          if (!e.children) {
            e.children = []
          }
          e.children.push(y) // æŠŠå­æ·»åŠ çˆ¶
        }
      })
    })
    arr = arr.filter((ele) => ele.pid === null) // è¿‡æ»¤
    return arr
  }
}

export default treeUtil
