import * as utils from '../../utils'
const { isFunction } = utils

// äº‹ä»¶çš„é…ç½®é€‰é¡¹å‚æ•°
export const defaultEventConfig = {
  // æŒ‡å®šæ˜¯å¦ä¸€æ¬¡æ€§çš„äº‹ä»¶
  isOnce: false,

  // è¿™ä¸ªæ˜¯å…¨å±€çš„é…ç½®ã€‚æŸä¸€ç±»äº‹ä»¶ï¼Œæœ‰ä¸€ä¸ªå›è°ƒæŒ‡å®šäº† defaultAction ä¼šå½±å“åˆ°æ‰€æœ‰æ³¨å†Œçš„å›è°ƒ
  // æ˜¯å¦æ‰§è¡Œé»˜è®¤çš„æ“ä½œ, æ¯”å¦‚ã€‚ç›‘æ§çš„ç‚¹å‡»ï¼Œé»˜è®¤ä¼šæ ¹æ®ç›¸æœºçš„è§†è§’è¿›è¡Œè·³è½¬ï¼Œä¼  false æ¥é˜»æ­¢é»˜è®¤çš„è¡Œä¸º
  defaultAction: true
}

/**
 * @class
 * @classdesc æ¨¡å‹äº‹ä»¶çš„åŸºç±»
 *            1. å®šä¹‰äº‹ä»¶çš„æ³¨å†Œæœºåˆ¶ã€‚ä¿è¯å„äº‹ä»¶éƒ½åªæ³¨å†Œä¸€æ¬¡ã€‚
 *            2. å®šä¹‰å„ç±»è®¾å¤‡é€šç”¨çš„äº‹ä»¶é›†åˆã€‚
 *             onClick: æ¨¡å‹ç‚¹å‡»
 *             onDoubleClick: æ¨¡å‹åŒå‡»
 *             onIconClick: å›¾æ ‡ç‚¹å‡»
 *             onIconDoubleClick: å›¾æ ‡åŒå‡»
 *             å„è®¾å¤‡è¿˜å¯ä»¥æ‰©å±•è‡ªå·±çš„å®ç°
 *
 *             TODOï¼šå®ç°å¯ä»¥é˜»æ­¢æŸä¸€ç±»è®¾å¤‡ï¼ŒæŸä¸€ä¸ªäº‹ä»¶çš„é»˜è®¤è¡Œä¸º
 *
 *           3. å…³äºäº‹ä»¶æ³¨å†Œçš„è¯´æ˜ï¼š
 *              å­ç±»åº”è¯¥å®šä¹‰è‡ªå·±çš„äº‹ä»¶ç±»å‹å‘½åç©ºé—´ã€‚ä»¥é˜²æ­¢ä¸åŒç±»å‹äº‹ä»¶å› ä¸ºäº‹ä»¶ç±»å‹åç§°ç›¸åŒè€Œå†²çª
 */
export class EventBase {
  constructor(mapViewer) {
    this.mapViewer = mapViewer

    // äº‹ä»¶é˜Ÿåˆ—
    this.events = new Map()

    // æ¯ä¸€ç±»è®¾å¤‡éƒ½é…ä¸€ä¸ªé»˜è®¤çš„é…ç½®é¡¹
    this.config = { ...defaultEventConfig }

    //* æ³¨æ„ï¼Œå­ç±»éœ€è¦è‡ªå·±è°ƒç”¨æ³¨å†Œäº‹ä»¶çš„å‡½æ•°: this.listen()
    //* åŸå› åœ¨äºã€‚this æŒ‡å‘åœ¨å›è°ƒä¸­éœ€è¦ç»‘å®šåˆ°å­ç±»
  }

  /**
   * å…¨å±€é…ç½®çš„
   */
  config(option = defaultEventConfig) {
    this.config = {
      ...defaultEventConfig,
      ...option
    }
  }

  /**
   * è·å–æŸä¸€ç±»è®¾å¤‡çš„å…¨å±€é…ç½®
   * @param key
   */
  getConfig(key) {
    const eventMap = this.events.get(key)

    if (eventMap) {
      return eventMap.get('config')
    }
    return null
  }

  /**
   * @desc äº‹ä»¶è®¢é˜…
   * @param key
   * @param callback
   * @param config
   */
  subscribe(key, callback, config = defaultEventConfig) {
    /**
     * events çš„ç»“æ„ä¸ºï¼š
     *  {
     *    key: {
     *      config: {} // global config,
     *      queue: [
     *      {
     *        callback: callback,
     *        config, config
     *      }
     *    ]
     *    }
     *  }
     *
     */
    const cachedEventMap = this.events.get(key)

    const callbackMap = new Map()
    callbackMap.set('callback', callback)
    callbackMap.set('config', config)

    if (cachedEventMap) {
      const eventQueue = cachedEventMap.get('queue')
      eventQueue.add(callbackMap)
      // æ›´æ–°æŸä¸ªäº‹ä»¶ç±»çš„å…¨å±€é…ç½®
      cachedEventMap.set('config', config)
    } else {
      const eventMap = new Map()
      const eventQueue = new Set()
      eventQueue.add(callbackMap)
      eventMap.set('queue', eventQueue)
      eventMap.set('config', config)
      this.events.set(key, eventMap)
    }
  }

  /**
   * ç§»é™¤äº‹ä»¶è®¢é˜… - TODO: å¾…éªŒè¯æ­£ç¡®æ€§
   * @param {String} key æŒ‡å®šè¦ç§»é™¤çš„æ˜¯ä»€ä¹ˆç±»å‹çš„äº‹ä»¶
   * @param {Function} callback
   */
  unsubscribe(key, callback) {
    const eventMap = this.events.get(key)
    if (eventMap) {
      const eventQueue = eventMap.get('queue')
      for (let eventMap of eventQueue.values()) {
        // ä»å›è°ƒé˜Ÿåˆ—ä¸­ï¼Œç§»é™¤è¿™ä¸ªå›è°ƒ
        if (eventMap.get('callback') === callback) {
          eventQueue.delete(eventMap)
        }
      }

      // å¦‚æœè¿™ä¸€ç±»äº‹ä»¶çš„å›è°ƒé˜Ÿåˆ—ç©ºäº†ï¼Œç›´æ¥ç§»é™¤è¿™ä¸€ç±»äº‹ä»¶çš„é˜Ÿåˆ—
      if (eventQueue.size === 0) {
        this.events.delete(key)
      }
    }
  }

  /**
   * @desc äº‹ä»¶åˆ†å‘
   * @param key
   * @param args
   */
  dispatch(key, ...args) {
    const eventMap = this.events.get(key)

    if (eventMap) {
      const eventQueue = eventMap.get('queue')
      // const globalConfig = eventMap.get('config')
      for (let eventMapItem of eventQueue.values()) {
        const callback = eventMapItem.get('callback')
        const callbackConfig = eventMapItem.get('config')
        if (isFunction(callback)) {
          callback(...args)
        }

        // å¯¹äºåªéœ€è¦è§¦å‘ä¸€æ¬¡çš„äº‹ä»¶ï¼Œè°ƒç”¨åç§»é™¤æ‰
        if (callbackConfig && callbackConfig.isOnce) {
          this.unsubscribe(key, callback)
        }
      }
    }
  }

  /**
   * @desc äº‹ä»¶åˆå§‹åŒ–çš„æŠ½è±¡æ–¹æ³• - éœ€è¦å„ç±»è®¾å¤‡è‡ªå·±å®ç°ã€‚å› ä¸ºå„ç±»è®¾å¤‡çš„å‰ç¼€åªæœ‰è‡ªå·±çŸ¥é“
   * @abstract
   */
  listen = () => {}

  /**
   * @desc æ¸…ç©ºæ‰€æœ‰ç›‘å¬çš„äº‹ä»¶
   // * @abstract
   */
  clear() {
    // å–æ¶ˆäº‹ä»¶è®¢é˜…
    // this.unsubscribe()

    // æ¸…ç©ºå›è°ƒåˆ—è¡¨
    this.events.clear()
  }

  /**
   * @abstract
   * @optional
   * @param callback
   * @param config
   */
  onClick(callback, config = defaultEventConfig) {}

  /**
   * @abstract
   * @optional
   * @param callback
   */
  offClick(callback) {}

  /**
   * @abstract
   * @optional
   * @param callback
   * @param config
   */
  onDoubleClick(callback, config = defaultEventConfig) {}

  /**
   * @abstract
   * @optional
   * @param callback
   */
  offDoubleClick(callback) {}

  /**
   * @abstract
   * @optional
   * @param callback
   * @param config
   */
  onIconClick(callback, config = defaultEventConfig) {}

  /**
   * @abstract
   * @optional
   * @param callback
   */
  offIconClick(callback) {}

  /**
   * @abstract
   * @optional
   * @param callback
   * @param config
   */
  onIconDoubleClick(callback, config = defaultEventConfig) {}

  /**
   * @abstract
   * @optional
   * @param callback
   */
  offIconDoubleClick(callback) {}

  /**
   * @desc æ¨¡å‹çš„ç‚¹å‡»äº‹ä»¶å›è°ƒï¼Œ éœ€è¦å­ç±»è‡ªå·±å®ç°
   * @abstract
   * @private
   */
  _modelClickHandler() {}

  /**
   * @desc æ¨¡å‹çš„åŒå‡»äº‹ä»¶å›è°ƒï¼Œ éœ€è¦å­ç±»è‡ªå·±å®ç°
   * @abstract
   * @private
   */
  _modelDoubleClickHandler() {}

  /**
   * @desc å›¾æ ‡çš„å•å‡»äº‹ä»¶å›è°ƒï¼Œ éœ€è¦å­ç±»è‡ªå·±å®ç°
   * @abstract
   * @private
   */
  _iconClickHandler() {}

  /**
   * @desc å›¾æ ‡çš„åŒå‡»å‡»äº‹ä»¶å›è°ƒï¼Œ éœ€è¦å­ç±»è‡ªå·±å®ç°
   * @abstract
   * @private
   */
  _iconDoubleClickHandler() {}
}
