  constructor(view3d, drawCtl) {
    this._view3d = view3d
    this._drawCtl = drawCtl
    this.config = drawCtl.config
    this.mapViewer = drawCtl._mapView
  }

  _draw(
    config,
    options = {
      isOnce: false,
      onDraw: NOOP,
      onFinish: NOOP,
      drawPoints: true,
      pointsVisible: true
    }
  ) {
    const mapViewer = this.mapViewer
    const { onDraw, onFinish, drawPoints, pointsVisible, isOnce } = options

    const self = this
    let entity = null
    let pointEntities = []

    // å…ˆç§»é™¤é¼ æ ‡ç‚¹å‡»äº‹ä»¶
    mapViewer.event.silent()

    const drawCtl = this._drawCtl

    const windowRightClickHandleRef = function windowRightClickHandleRef() {
      drawCtl.overLayerStopEdit()

      // å³é”®ä¸€æ¬¡åè§£ç»‘
      window.removeEventListener('contextmenu', windowRightClickHandleRef)

      if (drawPoints) {
        // åˆ é™¤ç‚¹ä½å¯¹è±¡
        for (let pointEntity of pointEntities) {
          drawCtl.overLayerRemoveObject(pointEntity)
        }
      }

      // é‡æ–°ç»‘å®šäº‹ä»¶
      mapViewer.event.reBindMouseClickCallback()

      if (isFunction(onFinish)) {
        onFinish(entity)
      }
    }

    window.addEventListener('contextmenu', windowRightClickHandleRef)

    // TOFIX: configä¸­æŒ‡å®šçš„GIDæ— æ•ˆ
    drawCtl.overLayerStartEdit(config, (res) => {
      entity = res
      if (isArray(res.points) && drawPoints) {
        // è¿™é‡Œåªæ·»åŠ æ–°å¢çš„ç‚¹
        for (let point of res.points.slice(pointEntities.length)) {
          self
            .point(
              {
                radius: 5,
                color: config.color,
                location: point
              },
              false
            )
            .then((pointEntity) => {
              pointEntities.push(pointEntity)
            })
        }
      }

      if (isFunction(onDraw)) {
        onDraw(res)
      }

      // å¯¹äºå•ä¸ªæ¨¡å‹çš„ç»˜åˆ¶,å¯ä»¥æ§åˆ¶æ˜¯å¦å€¼ç»˜åˆ¶ä¸€æ¬¡
      if (isOnce) {
        windowRightClickHandleRef()
      }
    })
  }

