// import { utils } from '@ty/shared'
import * as utils from './utils'

const { NOOP, isArray, isFunction, isPlainObject, isPromise } = utils

let taskQueue = []
let workloopRuning = false

/**
 * ç­‰å¾…æŒ‡å®šæ—¶é—´ä¹‹åï¼Œæ‰§è¡Œåé¢çš„ä»£ç , æˆ–è€…æ‰§è¡ŒæŒ‡å®šçš„å›è°ƒ
 * @param time é»˜è®¤100æ¯«ç§’
 * @param thisArg
 * @param fn
 * @param args
 *
 */
export function sleep(time = 100, thisArg, fn, ...args) {
  return new Promise((resolve) => {
    if (isFunction(thisArg)) {
      fn = thisArg
      thisArg = null
    }

    if (fn) {
      fn.apply(thisArg, [...args])
    }

    setTimeout(() => {
      let callback = NOOP
      if (args.length === 1 && isFunction(args[0])) {
        callback = args[0]
      } else if (args.length > 1 && isFunction(args[args.length - 1])) {
        callback = args[args.length - 1]
      }
      callback()
      resolve()
    }, time)
  })
}

/**
 * ç­‰å¾…å¼‚æ­¥å›è°ƒå‡½æ•°æ‰§è¡Œåæ‰ç»§ç»­å¾€ä¸‹æ‰§è¡Œ
 * @param thisArg {Object} è¦è°ƒç”¨çš„å¼‚æ­¥å‡½æ•°çš„å‘½åç©ºé—´
 * @param fn {Function} å®é™…è¦è°ƒç”¨çš„å¼‚æ­¥å‡½æ•°
 * @param args ä¼ ç»™å¼‚æ­¥å‡½æ•°çš„å‚æ•°åˆ—è¡¨
 *
 */
export function until(thisArg, fn, ...args) {
  return new Promise((resolve) => {
    if (isFunction(fn)) {
      let callback = (...arg) => {
        resolve(...arg)
      }
      // ç¬¬ä¸€ä¸ªå‚æ•°å°±æ˜¯å›è°ƒå‡½æ•°çš„æƒ…å†µ
      if (isArray(args) && args.length === 1 && isFunction(args[0])) {
        let innerCallback = args[0]
        callback = (...arg) => {
          innerCallback.apply(thisArg, [...arg])
          resolve(...arg)
        }
        args = []
        // æœ‰å‚æ•°çš„æƒ…å†µï¼Œä¸”æœ€åä¸€ä¸ªå‡½æ•°æ˜¯å›è°ƒ
      } else if (
        isArray(args) &&
        args.length > 1 &&
        isFunction(args[args.length - 1])
      ) {
        let preCallback = args.pop()
        callback = (...arg) => {
          preCallback.apply(null, [...arg])
          resolve(...arg)
        }
      }
      fn.apply(thisArg, [...args, callback])
    }
  })
}

/**
 * @param thisArg
 * @param fn
 * @param args
 *
 */
export function curry(thisArg, fn, ...args) {
  return async () => until(thisArg, fn, ...args)
}

/**
 *
 * @param time {Number}
 * @param thisArg {Object | null}
 * @param fn {Function}
 * @param args
 *
 */
export function wait(time, thisArg, fn, ...args) {
  return async () => sleep(time, thisArg, fn, ...args)
}

/**
 * å®é™…çš„è°ƒåº¦å‡½æ•°
 * @param asyncWork {Object}
 *
 */
export function doSchedule(asyncWork) {
  return new Promise(async (resolve, reject) => {
    try {
      performAsyncWork({
        task: asyncWork.task,
        complete: (...args) => {
          resolve(...args)
        }
      })
    } catch (e) {
      reject(e)
    }
  })
}

/**
 * @param cb
 */
export function preSchedule(cb) {
  return async (thisArg, fn, ...args) =>
    doSchedule({
      task: () => {
        if (isFunction(cb)) {
          cb && cb()
        }
        return curry(thisArg, fn, ...args)
      }
    })
}

export function schedule(thisArg, fn, ...args) {
  return doSchedule({
    task: curry(thisArg, fn, ...args)
  })
}

export function waitSchedule(time = 10, thisArg, fn, ...args) {
  return doSchedule({
    task: wait(time, thisArg, fn, ...args)
  })
}

/**
 * æ‰§è¡Œä¸€æ¬¡å¼‚æ­¥çš„ä»»åŠ¡
 * @param workUnit {Object}
 */
export function performAsyncWork(workUnit) {
  if (isPlainObject(workUnit)) {
    push(taskQueue, workUnit)
  }
  !workloopRuning && workloop()
}

export function peek(heap) {
  return heap.length === 0 ? null : heap[0]
}

export function push(heap, task) {
  heap.push(task)
}

export function pop(heap) {
  if (heap.length === 0) {
    return null
  }

  return heap.splice(0, 1)
}

async function workloop() {
  workloopRuning = true
  let currentTask = peek(taskQueue)
  while (currentTask !== null) {
    const asyncCallback = currentTask.task()
    let res
    if (isFunction(asyncCallback)) {
      res = await asyncCallback()
    } else if (isPromise(asyncCallback)) {
      res = await asyncCallback
    }
    const callback = currentTask.complete
    if (isFunction(callback)) {
      callback(res)
    }
    pop(taskQueue)
    currentTask = peek(taskQueue)
  }

  workloopRuning = false
}

window.$taskQueue = taskQueue
