// import { utils } from '@ty/shared'
import * as utils from '../utils'
import { functionExist, logError, warn } from '../error'

/**
 * @module sdk-event
 * @description sdkäº‹ä»¶
 */

const { isFunction, NOOP, isBoolean, FALSE_FUNC, debounce, throttle } = utils

// TODO: prefix ä¸åŒºåˆ†å¤§å°å†™

const MAP_STATUS_NORMAL = 0b0001 // æ™®é€šçŠ¶æ€
const MAP_STATUS_DRAG = 0b0010 // æ‹–åŠ¨çš„çŠ¶æ€

const ZOOM_IN_KEY = 'zoomIn'
const ZOOM_OUT_KEY = 'zoomOut'
const MOUSE_MOVE_KEY = 'mousemove'

let mapStatus = MAP_STATUS_NORMAL
let mapMoveTimer = null

const mouseEventGroup = [ZOOM_IN_KEY, ZOOM_OUT_KEY, MOUSE_MOVE_KEY]

const defaultConfig = {
  prefix: '', // å½“ isDouble ä¸ºtrueæ˜¯ï¼ŒåŸå§‹çš„prefix,ä¼šä¿å­˜åˆ°è¿™é‡Œ
  isDouble: false, // å†…éƒ¨åˆ¤æ–­æ˜¯å¦åŒå‡»äº‹ä»¶ï¼Œ
  compareFunc: FALSE_FUNC,
  isOnce: false,
  internal: false,
  key: +new Date() // å›è°ƒæŒ‡å®šçš„keyï¼Œåˆ é™¤æ—¶å¯ç”¨
}

/**
 * @class MapEvent
 */
class MapEvent {
  constructor(view3d, mapViewer) {
    this._view3d = view3d
    this.mapViewer = mapViewer
    this.prefix = '*'

    /*
     prefixAndCallbackMap æ˜¯ä¸€ä¸ªä¸‰å±‚ç»“æ„ï¼š
     {
     'prefix': {
      prefix: 'prefix',
      callback: {
         // å•å‡»çš„å›è°ƒ
         click: [{
         config: config,
         callback: callback
        }],

         // åŒå‡»çš„å›è°ƒ
         double: [{
           config: config,
           callback: callback
         }]
      }
     }
     */
    this.prefixAndCallbackMap = new Map()

    this._lastClickTimestamp = +new Date()

    this._emitMouseUp = false

    this._addMouseMoveEvent()
    this._addMouseUpEvent()
    this._addMouseDownEvent()

    // æ˜¯å¦æ³¨å†Œè¿‡é¼ æ ‡æ»šè½®äº‹ä»¶
    this._hasRegisterMouseWheelHandler = false

    // åœ¨ç‚¹å‡»äº‹ä»¶è§¦å‘æ—¶ï¼Œå¦‚æœéƒ½æ²¡æœ‰åŒ¹é…åˆ°æ³¨å†Œçš„äº‹ä»¶å›è°ƒï¼Œè¦æ‰§è¡Œä¸Šä¸€æ¬¡å›è°ƒçš„æ¸…ç†å‡½æ•°
    this.cleanupCallback = null

    // åŒä¸€ä¸ªprefixï¼ŒåŒæ—¶æ³¨å†Œå•å‡»å’ŒåŒå‡»äº‹ä»¶ï¼Œåœ¨300mså›è°ƒåæ¥åˆ¤æ–­æ˜¯å¦æ‰§è¡Œå•å‡»
    this._clickCallbackTimer = new Map()

    // ç›‘æ§é¼ æ ‡æ‹–åŠ¨çš„äº‹ä»¶å›è°ƒ
    this._onDragHandler = new Set()

    // å¼€å§‹æ‹–åŠ¨æ—¶çš„å›è°ƒ
    this._onDragStartHandler = new Set()

    // åœæ­¢æ‹–åŠ¨æ—¶çš„å›è°ƒ
    this._onDragEndHandler = new Set()

    // äº‹ä»¶ç³»ç»Ÿå®ä¾‹åŒ–çš„æ—¶å€™ï¼Œç›´æ¥æ³¨å†Œäº‹ä»¶ã€‚ä¸åŒåœ¨ onClick é‡Œæ¯æ¬¡éƒ½é‡å¤æ³¨å†Œ
    // this.reBindMouseClickCallback()

    // å“åº”ç‚¹å‡»å’Œæ˜¯å¦æ‹–åŠ¨çš„æ—¶é—´é—´éš”ï¼Œåœ¨è¿™ä¸ªé—´éš”å†…ï¼Œå¦‚æœè§¦å‘äº†åœ°å›¾çš„æ‹–åŠ¨ï¼Œå°†ä¸å“åº”åœ°å›¾çš„ç‚¹å‡»äº‹ä»¶ã€‚
    this._dragTicker = 150
  }

  _flushDragCallback = (callbackSet, e) => {
    if (callbackSet.size > 0) {
      callbackSet.forEach((callback) => {
        callback(e)
      })
    }
  }

  _handleMouseMove(e) {
    const self = this

    // ä»é¼ æ ‡æŒ‰ä¸‹ï¼Œåˆ°è§¦å‘ç¬¬ä¸€æ¬¡æ‹–åŠ¨ï¼Œè°ƒåº¦ä¸€ä¸‹å¼€å§‹æ‹–åŠ¨çš„å›è°ƒ
    if (mapStatus === MAP_STATUS_NORMAL) {
      this._flushDragCallback(this._onDragStartHandler, e)
    }

    mapStatus = MAP_STATUS_DRAG

    if (mapMoveTimer) {
      clearTimeout(mapMoveTimer)
    }

    // æ‹–åŠ¨è¿‡ç¨‹ä¸­ï¼Œè°ƒåº¦æ‹–æ‹½äº‹ä»¶
    this._flushDragCallback(this._onDragHandler, e)

    // å¦‚æœç§»åŠ¨è¿‡ç¨‹ä¸­ï¼Œé¼ æ ‡ç§»åˆ°å¼¹çª—ä¸Šï¼Œå¯èƒ½æ²¡æœ‰è§¦å‘mouseupäº‹ä»¶ï¼Œè¿™é‡Œç»™ä¸ªå®šæ—¶å™¨ï¼Œ100æ¯«ç§’ä»¥åï¼Œç§»é™¤æ‹–åŠ¨çŠ¶æ€
    mapMoveTimer = setTimeout(() => {
      if (!self._emitMouseUp) {
        self._handleMouseUp()
      }
    }, 100)
  }

  _handleMouseDown() {
  }

  _handleMouseUp() {
    // ä»æ‹–æ‹½çš„çŠ¶æ€é‡Šæ”¾é¼ æ ‡ï¼Œè°ƒåº¦ä¸€æ¬¡æ‹–æ‹½ç»“æŸæ—¶é—´
    if (mapStatus === MAP_STATUS_DRAG) {
      this._flushDragCallback(this._onDragEndHandler)
    }

    mapStatus = MAP_STATUS_NORMAL
  }

  _registerMouseEvent(key, callback) {
    const mapViewContainerId = this.mapViewer.core.config.id
    const $mapViewContainer = document.getElementById(mapViewContainerId)
    $mapViewContainer.addEventListener(key, callback)
  }

  _removeMouseEvent(key, callback) {
    const existCallbackMapKey = mouseEventGroup.filter(
      (mouseKey) => mouseKey === key
    )[0]
    if (existCallbackMapKey) {
      const existCallbackMap =
        this.prefixAndCallbackMap.get(existCallbackMapKey)
      if (existCallbackMap) {
        // åˆ é™¤ç¼“å­˜çš„å‡½æ•°å‰,å‰ä¿å­˜ä¸€ä¸‹
        const cachedCallback = this.getCallbacks(key)[0].callback

        // å†å»åˆ é™¤ç¼“å­˜çš„å‡½æ•°
        this._removeCacheCallback(key, callback)

        if (
          (!this.prefixAndCallbackMap.get(ZOOM_IN_KEY) &&
            !this.prefixAndCallbackMap.get(ZOOM_OUT_KEY)) ||
          key === MOUSE_MOVE_KEY
        ) {
          const mapViewContainerId = this.mapViewer.core.config.id
          const $mapViewContainer = document.getElementById(mapViewContainerId)
          // ç§»é™¤DOMå…ƒç´ ä¸Šçš„å›è°ƒ
          $mapViewContainer.removeEventListener(key, cachedCallback)
        }
      }
    }
  }

  _registerMouseWheelCallback(key = 'mousewheel', callback = NOOP) {
    // ä¸é‡å¤ç›‘å¬é¼ æ ‡æ»šè½®äº‹ä»¶
    if (this._hasRegisterMouseWheelHandler) {
      return
    }
    this._registerMouseEvent(key, callback)

    this._hasRegisterMouseWheelHandler = true
  }

  async _mouseWheelHandler(e) {
    let wheelDelta = e.wheelDelta
    let eventTypeKey = ''
    // zoomIn
    if (wheelDelta > 0) {
      eventTypeKey = ZOOM_IN_KEY
      // zoomOut
    } else if (wheelDelta < 0) {
      eventTypeKey = ZOOM_OUT_KEY
    }

    if (
      eventTypeKey &&
      this.mapViewer.camera &&
      this.mapViewer.camera.getCurrentPosition
    ) {
      // ç¼©æ”¾çš„åŒæ—¶ï¼Œè·å–ä¸€æ¬¡å½“å‰çš„ç›¸æœºä½ç½®ï¼Œä¸»è¦ç”¨æ¥åˆ¤æ–­å½“å‰çš„é«˜åº¦
      this.mapViewer.camera.getCurrentPosition().then((position) => {
        const mouseWheelMap = this.prefixAndCallbackMap.get(eventTypeKey)
        if (mouseWheelMap) {
          const mouseWheelEventCallbackMap = mouseWheelMap.get('callback')
          const mouseWheelEventCallbackSet =
            mouseWheelEventCallbackMap.get('click')
          mouseWheelEventCallbackSet.forEach((oneMouseWheelHandlerMap) => {
            if (oneMouseWheelHandlerMap.get('config').compareFunc) {
              oneMouseWheelHandlerMap.get('config').compareFunc(e, position)
            }
          })
        }
      })
    }
  }

  getCallbacks(prefix) {
    const callbackMap = this.prefixAndCallbackMap.get(prefix)
    if (callbackMap) {
      return Array.from(callbackMap.get('callback').click).map(
        (oneCallbackMap) => Object.fromEntries(oneCallbackMap)
      )
    }
  }

  /**
   * @desc è®¾ç½®å“åº”ç‚¹å‡»å’Œç¡®è®¤æ˜¯å¦æ‹–åŠ¨çš„æ—¶é—´é—´éš”ï¼Œ é»˜è®¤ 150ms.
   */
  setDragTicker(time = 150) {
    this._dragTicker = time
  }

  // è§†è§’æ”¾å¤§
  zoomIn(
    callback,
    config = {
      debounce: true,
      debounceTime: 300
    }
  ) {
    if (isFunction(callback)) {
      let zoomInHandler = this._mouseWheelHandler.bind(this)
      if (config.debounce) {
        zoomInHandler = debounce(
          this._mouseWheelHandler.bind(this),
          config.debounceTime || 300,
          false
        )
      }

      this._cacheCallback(ZOOM_IN_KEY, zoomInHandler, {
        isOnce: false,
        compareFunc: callback,
        internal: false
      })
      this._registerMouseWheelCallback('mousewheel', zoomInHandler)
    }
  }

  // è§†è§’ç¼©å°
  zoomOut(
    callback,
    config = {
      debounce: true,
      debounceTime: 300
    }
  ) {
    if (isFunction(callback)) {
      let zoomOutHandler = this._mouseWheelHandler.bind(this)
      if (config.debounce) {
        zoomOutHandler = debounce(
          this._mouseWheelHandler.bind(this),
          config.debounceTime || 300,
          false
        )
      }
      this._cacheCallback(ZOOM_OUT_KEY, zoomOutHandler, {
        isOnce: false,
        compareFunc: callback,
        internal: false
      })
      this._registerMouseWheelCallback('mousewheel', zoomOutHandler)
    }
  }

  // æ³¨å†Œé¼ æ ‡ç§»åŠ¨äº‹ä»¶å¯èƒ½å¯¼è‡´åœ°å›¾ååº”æ…¢.å…ˆä¸å¤„ç†
  onMouseMove(callback) {
    const mouseMoveHandler = throttle((e) => {
      console.log(e)
    }, 500)

    this._cacheCallback(MOUSE_MOVE_KEY, mouseMoveHandler, {
      isOnce: false,
      compareFunc: callback,
      internal: false
    })

    this._registerMouseEvent(MOUSE_MOVE_KEY, mouseMoveHandler)
  }

  // æ‹–åŠ¨åœ°å›¾çš„æ—¶å€™
  onDrag(callback) {
    if (isFunction(callback)) {
      this._onDragHandler.add(callback)
    }
  }

  onDragStart(callback) {
    if (isFunction(callback)) {
      this._onDragStartHandler.add(callback)
    }
  }

  onDragEnd(callback) {
    if (isFunction(callback)) {
      this._onDragEndHandler.add(callback)
    }
  }

  removeDrag(callback) {
    if (isFunction(callback)) {
      this._onDragHandler.delete(callback)
    }
  }

  removeDragStart(callback) {
    if (isFunction(callback)) {
      this._onDragStartHandler.delete(callback)
    }
  }

  removeDragEnd(callback) {
    if (isFunction(callback)) {
      this._onDragEndHandler.delete(callback)
    }
  }

  /**
   * ç§»é™¤é¼ æ ‡ onmousemove, onmousedown, onmouseupäº‹ä»¶
   */
  removeMouseEvent() {
    if (this._view3d && functionExist(this._view3d, 'UnRegisterEvent')) {
      this._view3d.UnRegisterEvent()
    }
  }

  // ç›‘å¬é¼ æ ‡æŒ‰ä½å¹¶æ‹–åŠ¨åœ°å›¾çš„äº‹ä»¶
  _addMouseMoveEvent() {
    const view3d = this._view3d
    if (functionExist(view3d, 'RegisterEvent')) {
      view3d.RegisterEvent('onmousemove', this._handleMouseMove.bind(this))
    }
  }

  _addMouseDownEvent() {
    const view3d = this._view3d
    if (functionExist(view3d, 'RegisterEvent')) {
      view3d.RegisterEvent('onmousedown', this._handleMouseDown.bind(this))
    }
  }

  // é¼ æ ‡æŠ¬èµ·
  _addMouseUpEvent() {
    const view3d = this._view3d
    if (functionExist(view3d, 'RegisterEvent')) {
      view3d.RegisterEvent('onmouseup', this._handleMouseUp.bind(this))
    }
  }

  _resetMouseCallback() {
    this.prefix = '*'

    for (let k of this.prefixAndCallbackMap.keys()) {
      this.prefixAndCallbackMap.delete(k)
    }
  }

  _cacheCallback(prefix, callback, config) {
    // prefix: '*', 'V', 'TEMP,PIC' ç­‰å½¢å¼
    if (!prefix) {
      return
    }

    const prefixArr = prefix.split(',').map((p) => p.trim())

    // æ ¹æ®å‰ç¼€åšç¼“å­˜
    for (let k of prefixArr) {
      // åˆ©ç”¨ Set å”¯ä¸€æ€§ï¼Œé¿å…å›è°ƒå¤šæ¬¡é‡å¤çš„è¢«æ‰§è¡Œ
      const existCallbackMap = this.prefixAndCallbackMap.get(k)
      if (existCallbackMap) {
        if (existCallbackMap.has('callback')) {
          // const callbackSet = existCallbackMap.get('callback')
          const callbackMap = existCallbackMap.get('callback')
          const oneCallbackMap = new Map()
          oneCallbackMap.set('config', config)
          oneCallbackMap.set('callback', callback)

          // callbackSet.add(oneCallbackMap)
          let existCallbackSet
          if (config.isDouble) {
            existCallbackSet = callbackMap.get('double')
          } else {
            existCallbackSet = callbackMap.get('click')
          }
          existCallbackSet.add(oneCallbackMap)
        }
      } else {
        const prefixCallbackMap = new Map()
        const callbackConfigMap = new Map()
        const clickCallbackConfigSet = new Set()
        const doubleClickCallbackConfigSet = new Set()

        // const callbackConfigSet = new Set()

        const oneCallbackMap = new Map()
        oneCallbackMap.set('config', config)
        oneCallbackMap.set('callback', callback)

        if (config.isDouble) {
          doubleClickCallbackConfigSet.add(oneCallbackMap)
        } else {
          clickCallbackConfigSet.add(oneCallbackMap)
        }

        // callbackConfigSet.add(oneCallbackMap)
        callbackConfigMap.set('click', clickCallbackConfigSet)
        callbackConfigMap.set('double', doubleClickCallbackConfigSet)

        prefixCallbackMap.set('prefix', k)
        prefixCallbackMap.set('callback', callbackConfigMap)
        this.prefixAndCallbackMap.set(k, prefixCallbackMap)
      }
    }
  }

  /**
   * å®é™…å¤„ç†ç§»é™¤äº‹ä»¶çš„é€»è¾‘ï¼Œ
   * @param oldPrefix
   * @param callback {Function} ç§»é™¤äº‹ä»¶å›è°ƒæ—¶ï¼Œå¿…é¡»ä¼ çš„ã€‚é€šè¿‡äº‹ä»¶å¼•ç”¨æ¥æŸ¥æ‰¾å¯¹åº”çš„å›è°ƒ
   * @private
   */
  _removeCacheCallback(oldPrefix, callback) {
    if (!oldPrefix) {
      return
    }

    const oldPrefixArr = oldPrefix.split(',')
    for (let k of oldPrefixArr) {
      if (this.prefixAndCallbackMap.has(k)) {
        const callbackMap = this.prefixAndCallbackMap.get(k)
        if (callbackMap) {
          // const oneCallbackSet = callbackMap.get('callback')
          const oneCallbackMap = callbackMap.get('callback')
          const oneClickCallbackSet = oneCallbackMap.get('click')
          const oneDoubleClickCallBackSet = oneCallbackMap.get('double')

          const removeCallback = (oneCallbackSet) => {
            oneCallbackSet.forEach((oneCallbackMap) => {
              const cachedCallback = oneCallbackMap.get('callback')
              if (mouseEventGroup.includes(k)) {
                if (oneCallbackMap.get('config').compareFunc === callback) {
                  oneCallbackSet.delete(oneCallbackMap)
                }
              } else {
                if (cachedCallback === callback) {
                  oneCallbackSet.delete(oneCallbackMap)
                }
              }
            })
          }

          if (oneClickCallbackSet.size > 0) {
            removeCallback(oneClickCallbackSet)
          }

          if (oneDoubleClickCallBackSet.size > 0) {
            removeCallback(oneDoubleClickCallBackSet)
          }

          if (
            oneClickCallbackSet.size === 0 &&
            oneDoubleClickCallBackSet.size === 0
          ) {
            this.prefixAndCallbackMap.delete(k)
          }
        }
      }
    }
  }

  _addMouseClickCallback() {
    const view3d = this._view3d
    const self = this

    this.mapViewer.core.setParameters({
      prefix: '*'
    })

    if (functionExist(view3d, 'SetMouseCallback')) {
      const handleClickCallback = (res) => {
        self._lastClickCallbackTimer = setTimeout(() => {
          if (mapStatus === MAP_STATUS_DRAG) {
            // æ‹–åŠ¨çš„æ—¶å€™ï¼Œä¸å“åº”ç‚¹å‡»äº‹ä»¶
            return
          }
          const gid = res.gid
          // åŒæ—¶å– attr é‡Œçš„GIDï¼Œ å› ä¸ºæœ‰è‡ªå·±æŒ‡å®šGID æ— æ•ˆï¼Œåœ°å›¾ä¼šè‡ªå·±ç”Ÿæˆ gid,ä¸ºäº†è¿˜æ˜¯èƒ½åŒ¹é…ä¸Šç‚¹å‡»äº‹ä»¶ï¼Œ
          const uuid = (res.attr && res.attr.gid) || ''
          const now = +new Date()
          if (gid || uuid) {
            let matchCallback = false
            const prefixMapArray = Array.from(
              this.prefixAndCallbackMap.entries()
            )

            const triggerCallback = (prefix, cachedCallbackSet) => {
              cachedCallbackSet.forEach((callbackConfigMap) => {
                const oneCallbackConfig = callbackConfigMap.get('config')
                const oneCallback = callbackConfigMap.get('callback')

                const {
                  compareFunc,
                  isOnce
                  // prefix
                } = oneCallbackConfig

                // ç¬¬ä¸€ä¸ªä¸º compareFuncï¼Œ ä¸”å±äºç”¨æˆ·è‡ªå®šä¹‰çš„æ¯”è¾ƒå‡½æ•°
                if (compareFunc !== FALSE_FUNC) {
                  // è°ƒç”¨ç”¨æˆ·æŒ‡å®šçš„æ¯”è¾ƒå‡½æ•°ï¼Œä¼ å…¥åœ°å›¾ GIDï¼Œå’Œè‡ªå·±æŒ‡å®šçš„ gid ç»™ç”¨æˆ·åšåˆ¤æ–­
                  if (compareFunc(gid, uuid)) {
                    const cleanup = oneCallback(res)
                    if (isFunction(cleanup)) {
                      self.cleanupCallback = cleanup
                    }
                    matchCallback = true
                  }
                } else {
                  if (
                    (gid &&
                      gid
                        .toLocaleLowerCase()
                        .startsWith(prefix.toLocaleLowerCase())) ||
                    (uuid &&
                      uuid
                        .toLocaleLowerCase()
                        .startsWith(prefix.toLocaleLowerCase())) ||
                    prefix === '*'
                  ) {
                    matchCallback = true
                    const cleanup = oneCallback(res)
                    if (isFunction(cleanup)) {
                      self.cleanupCallback = cleanup
                    }
                  }
                }
                // æ ¹æ®æ¯ä¸ªå›è°ƒçš„å‚æ•°å’Œ prefix å¤„ç†inOnceçš„æƒ…å½¢
                if (isOnce) {
                  setTimeout(() => {
                    self.remove(prefix, oneCallback)
                  })
                }
              })
            }

            for (let i = 0; i < prefixMapArray.length; i++) {
              let [k, callbackMap] = prefixMapArray[i]
              const cachedPrefix = callbackMap.get('prefix')
              const cachedCallbackMap = callbackMap.get('callback')

              const clickCallbackSet = cachedCallbackMap.get('click') // å•å‡»çš„å›è°ƒ
              const doubleCallbackSet = cachedCallbackMap.get('double') // åŒå‡»çš„å›è°ƒ

              // åªæœ‰å•å‡»çš„å›è°ƒ
              if (clickCallbackSet.size > 0 && doubleCallbackSet.size === 0) {
                triggerCallback(cachedPrefix, clickCallbackSet)

                // åªæœ‰åŒå‡»çš„å›è°ƒ
              } else if (
                clickCallbackSet.size === 0 &&
                doubleCallbackSet.size > 0
              ) {
                if (now - self._lastClickTimestamp <= 300) {
                  triggerCallback(cachedPrefix, doubleCallbackSet)
                }

                // æ—¢æœ‰å•å‡»ï¼Œåˆæœ‰åŒå‡»çš„å›è°ƒ. å¦‚æœåœ¨300mså†…ï¼Œæ²¡æœ‰è§¦å‘åŒä¸€ä¸ªprefixçš„å›è°ƒï¼Œ
                // åˆ™è§¦å‘ä¸€æ¬¡å•å‡»çš„å›è°ƒã€‚å¦‚æœè§¦å‘äº†ï¼Œåˆ™è§¦å‘åŒå‡»çš„å›è°ƒ
              } else {
                let triggerDouble = false
                if (now - self._lastClickTimestamp <= 300) {
                  clearTimeout(self._clickCallbackTimer.get(cachedPrefix))
                  self._clickCallbackTimer.delete(cachedPrefix)
                  triggerDouble = true
                  triggerCallback(cachedPrefix, doubleCallbackSet)
                }
                if (!triggerDouble) {
                  const timerId = setTimeout(() => {
                    triggerCallback(cachedPrefix, clickCallbackSet)
                  }, 300)

                  self._clickCallbackTimer.set(cachedPrefix, timerId)
                }
              }
            }

            // è§¦å‘ä¸€æ¬¡ä¸åŒ¹é…çš„å›è°ƒï¼Œå¯ä»¥ç”¨æ¥æ¸…é™¤ä¸Šä¸€æ¬¡å›è°ƒé‡Œçš„äº‹
            if (!matchCallback && isFunction(self.cleanupCallback)) {
              self.cleanupCallback()
              // æ‰§è¡Œå®Œæ¯•ï¼Œç½®ç©ºï¼Œé¿å…é‡å¤æ‰§è¡Œ
              self.cleanupCallback = null
            }
          } else {
            // è§¦å‘ä¸€æ¬¡ä¸åŒ¹é…çš„å›è°ƒï¼Œå¯ä»¥ç”¨æ¥æ¸…é™¤ä¸Šä¸€æ¬¡å›è°ƒé‡Œçš„äº‹
            if (isFunction(self.cleanupCallback)) {
              self.cleanupCallback()
              // æ‰§è¡Œå®Œæ¯•ï¼Œç½®ç©ºï¼Œé¿å…é‡å¤æ‰§è¡Œ
              self.cleanupCallback = null
            }
          }
          self._lastClickTimestamp = now
        }, self._dragTicker)
      }
      // view3d.SetMouseCallback(null)
      setTimeout(() => {
        view3d.SetMouseCallback(handleClickCallback)
      }, 10)
    }
  }

  /**
   * ç›‘å¬é¼ æ ‡çš„ç‚¹å‡»,åå›ç‚¹å‡»ç‚¹çš„ä½ç½®
   * @param callback {Function}
   * @param isOnce {Boolean} ç‚¹å‡»äº‹ä»¶æ˜¯å¦åªå“åº”ä¸€æ¬¡
   */
  setMousePositionCallback(callback = NOOP, isOnce = true) {
    const view3d = this._view3d
    if (!isFunction(callback)) {
      logError(` callback must be a function, but got: ${typeof callback}.`)
      return
    }

    if (functionExist(view3d, 'SetMousePositionCallback')) {
      view3d.SetMousePositionCallback((position) => {
        callback(position)

        if (isOnce) {
          view3d.SetMousePositionCallback(null)
        }
      })
    }
  }

  /**
   * @description ç›‘å¬æŒ‡å®šå‰ç¼€æ¨¡å‹çš„ç‚¹é€‰äº‹ä»¶
   * @param prefix {String | Function}
   * @param callback {Function | Boolean}
   * @param config {Object}
   * @param config.compareFunc {Function}
   * @param config.isOnce {Boolean}
   * @param config.internal {Boolean}
   */
  onClick(prefix = '*', callback, config = defaultConfig) {
    config = Object.assign({}, defaultConfig, config)
    let { isOnce } = config
    if (isFunction(prefix)) {
      if (isBoolean(callback)) {
        isOnce = callback
      }
      callback = prefix
    }

    if (!isFunction(callback)) {
      logError(` callback must be a function, but got: ${typeof callback}.`)
      return
    }

    this._cacheCallback(prefix, callback, config)

    this._addMouseClickCallback()
  }

  reBindMouseClickCallback() {
    this._addMouseClickCallback()
  }

  /**
   * @description è®¾ç½®åŒå‡»äº‹ä»¶
   * @function
   * @param prefix
   * @param callback
   * @param config
   */
  setMouseDoubleClickCallback(prefix = '*', callback, config) {
    // const self = this
    this.onClick(
      prefix,
      // (res) => {
      // const now = +new Date()
      // // ä¸¤ä¸ªç‚¹å‡»å¦‚æœé—´éš”åœ¨100æ¯«ç§’ä»¥å†…ï¼Œ å“åº”ä¸€æ¬¡åŒå‡»äº‹ä»¶å³å¯
      // if (now - self._lastClickTimestamp <= 300) {
      //   callback(res)
      // }
      // self._lastClickTimestamp = now
      // },
      callback,
      {
        ...config,
        isDouble: true,
        prefix
      }
    )
  }

  /**
   * ç§»é™¤æŒ‡å®šå‰ç¼€çš„é¼ æ ‡äº‹ä»¶
   * @param prefix {String}
   * @param callback {Function}
   */
  remove(prefix, callback) {
    if (!prefix) {
      warn(`prefix is not defined, or is ''.`)
      return
    }
    // åœ°å›¾å®¹å™¨ä¸Šçš„é¼ æ ‡äº‹ä»¶è¦å•ç‹¬å¤„ç†, å› ä¸ºè¦ç§»é™¤DOMä¸Šçš„äº‹ä»¶
    if (mouseEventGroup.includes(prefix)) {
      this._removeMouseEvent(prefix, callback)
    } else {
      this._removeCacheCallback(prefix, callback)
    }
  }

  removeMouseDoubleClickCallback(prefix, callback) {
    this.remove(prefix, callback)
  }

  removeMousePositionCallback() {
    const view3d = this._view3d

    if (functionExist(view3d, 'SetMousePositionCallback')) {
      view3d.SetMousePositionCallback(null)
    }
  }

  /**
   * ç§»é™¤æ·»åŠ çš„æ‰€æœ‰å‰ç¼€çš„ç‚¹å‡»äº‹ä»¶
   */
  clear() {
    const view3d = this._view3d
    this.mapViewer.core.setParameters(null)
    if (functionExist(view3d, 'SetMouseCallback')) {
      view3d.SetMouseCallback(null)
      this._resetMouseCallback()
    }
  }

  /**
   * ç§»é™¤é¼ æ ‡ç‚¹å‡»äº‹ä»¶çš„ç›‘å¬,ä½†ä¿ç•™å·²æ³¨å†Œçš„äº‹ä»¶å›è°ƒã€‚åç»­å¯é€šè¿‡ reBindMouseClickCallback æ¥æ¢å¤äº‹ä»¶çš„æ‰§è¡Œ
   */
  silent() {
    const view3d = this._view3d
    view3d.Clear()
    view3d.SetParameters(null)
    view3d.SetMouseCallback(null)
  }
}

export default MapEvent
